<template lang="jade">
  div(@mouseenter.stop="userPanelIn", @mouseover.stop="isMaxthon ? userPanelIn : 0", @mouseleave.stop="userPanelOut", @mouseout.stop="isMaxthon ? userPanelOut : 0")

    a(href="/i", target="_blank")
      div.user-avatar.p-relative.b-circle.pointer.bg-center.bg-no-repeat.bg-cover.ts-dot-4(role="img", title="点击进入直播中心  (=・ω・=)", :class="{'active': show}", :style="{'background-image': 'url(' + userData.avatar + ')'}")
      div.msg-hinter.p-absolute.b-circle.a-splashing(v-show="userData.msgCount")

    div.user-panel.p-relative.border-box.none-select(v-show="show", transition="fade-out")

      //- Header Node.
      div.header-node.w-100.p-relative.border-box

        //- Username.
        h4.username-info.dp-none.p-relative.t-center.a-move-in-left(v-el:seq-in-1)
          span.text.dp-i-block.v-top.t-over-hidden.m-auto(:title="userData.username", v-text="userData.username")
          a(href="/i#to-vip", target="_blank", title="要来一发姥爷么？ (=・ω・=)")
            i.live-icon.dp-i-block(:class="{'vip-color': userData.vip === 'vip', 'vip-year-color': userData.vip === 'svip', 'vip-gray': !userData.vip}", style="margin-left: 5px; margin-top: 2px")

        //- User Level.
        div.user-level.p-absolute.dp-none.w-100.f-clear.t-center.pointer.border-box.a-move-in-left(v-el:seq-in-2, :title="'升级进度：' + userData.rank.expPercent")
          span.user-level-text.t-right.f-left(v-text="'UL.' + userData.rank.level")
          span.user-level-text.t-left.f-right(v-text="'UL.' + (userData.rank.level + 1)")
          div.level-progress.p-absolute.over-hidden
            div.progress-bar.h-100(:style="{'width': userData.rank.expPercent}")
          level-detail(:level="userData.rank.level", :rank="userData.rank.rank", :exp-left="userData.rank.levelExp - userData.rank.exp")

      div.content-ctnr.border-box.p-relative

        // Info Items.
        div.section-block.info-items.dp-none.a-move-in-left(v-el:seq-in-3)
          info-item(v-for="item in infoItems", :icon-name="item.iconName", :label="item.label", :value="item.value(this)")
            hr(v-if="$index === 1", style="margin: 15px 0")

        hr.dp-none.a-move-in-left(v-el:seq-in-4, style="margin: 15px 0 5px 0")

        //- Control Buttons.
        div.section-block.ctrl-btns.dp-none.a-move-in-left(v-el:seq-in-5)
          template(v-for="item in ctrlBtns")
            a.ctrl-btn.dp-i-block.pointer.t-center.ts-dot-2(:class="{'wide': item.wide, 'colored': item.colored}", :wide="item.wide", :href="item.href", @click="item.onClick(this)", :target="item.target || '_blank'")
              i.live-icon.v-middle(v-if="item.iconName", :class="item.iconName + '-dark'")
              span.v-middle(v-text="item.label")
              partial(:name="item.slot.name")
            hr(v-if="$index === 1", style="margin: 5px 0 10px 0")

      live-progress(:show="loading")
</template>

<style lang="stylus">
  @import "~@bilibili-live/utils/style/variable.styl";

  $userPanel = {
    width: 295px
    fontSize: 13px
    color: $biliColor.lightgray
  }

  $userLevelProgress = {
    height: 8px
  }

  $avatarSize = {
    closed: {
      radius: 40px
    }
  }

  $headerNode = {
    height: 105px
  }

  .user-panel-ctnr  // Parent component.

    .progress-tv
      border-radius: 0 0 5px 5px

    .msg-hinter
      width: 7px
      height: 7px
      top: 0
      left: 0
      margin: 5px 85px
      background-color: $biliColor.pink

    .user-avatar
      width: $avatarSize.closed.radius
      height: $avatarSize.closed.radius
      margin: 5px auto
      background-color: aliceblue
      transform-origin: 50% 0
      z-index: 101

      &.active
        transform: scale(1.8) translate(0, 5px)

        &:before
          content: ""
          display: block
          background-color: $biliColor.cyan
          width: 100%
          height: 100%
          position: relative
          border-radius: 50%
          opacity: 0
          animation: wave-splashing-avatar $cubicEase .6s forwards
          z-index: 1

    @keyframes wave-splashing-avatar
      0%
        display: block
        opacity: 1
        transform: scale(1)
      99%
        display: block
        opacity: 0
        transform: scale(3)
      100%
        display: none

    // User Panel.
    .user-panel
      min-height: 150px
      width: $userPanel.width
      margin-left: ($userPanel.width / 2) * -1
      left: 50%
      background-color: #fff
      color: $userPanel.color
      border-radius: 0 0 5px 5px

      // Header Node.
      .header-node
        background-color: $biliColor.darkblue
        height: $headerNode.height
        padding-top: 40px
        z-index: 0

        .username-info
          margin: 10px 0
          font-size: 18px
          color: #fff

          .text
            max-width: 80%

        .user-level
          left: 0
          bottom: 0
          padding: 10px 0px
          font-size: 14px
          color: #b8d3e6

          &:hover
            .user-level-detail
              display: block

          .user-level-text
            display: block
            width: 60px


        .level-progress
          height: $userLevelProgress.height
          background: #197499
          left: 68px
          right: 68px
          bottom: 14px

          .progress-bar
            width: 50px
            $biliColor.orange = #FFC257
            background-color: $biliColor.orange
            animation: scale-x $cubicEase .4s
            transform-origin: 0 50%

            @keyframes scale-x
              from
                transform: scaleX(0)
              to
                transform: scaleX(1)

      hr
        border: 0
        border-top: 1px solid #bbb

      .content-ctnr
        padding: 10px 20px 15px 20px
        border: 1px solid #ddd
        border-top: 0
        border-radius: 0 0 5px 5px
        font-size: $userPanel.fontSize

        .info-items
          margin: 10px 0

        .ctrl-btn
          $ctrlBtn = {
            width: 50%,
            fullWidth: 100%,
            margin: 10px
          }

          width: "calc(%s - %s)" % ($ctrlBtn.width $ctrlBtn.margin)
          padding: 8px 0
          margin: 2px 0
          color: $userPanel.color
          border-radius: 5px

          &:nth-child(odd)
            margin-right: $ctrlBtn.margin

          &:nth-child(even)
            margin-left: $ctrlBtn.margin

          &.wide
          &.wide:nth-child(odd)
          &.wide:nth-child(even)
            width: $ctrlBtn.fullWidth
            margin-left: 0
            margin-right: 0

          &:hover
            background-color: $biliColor.hovergray
            color: $biliColor.cyan

          &:active
            background-color: $biliColor.cyan
            color: #fff

          &.colored
            color: $biliColor.cyan

            &:active
              background-color: $biliColor.cyan
              color: #fff

          .live-icon
            margin-right: 5px

        .msg-count
          padding: 1px 5px
          margin-left: 5px
          border-radius: 9px
          background-color: $biliColor.lightpink
          color: #fff

    .a-move-in-left
      animation-duration: .6s

    // Fade-out Transition.
    .fade-out-leave
      animation: fade-out $cubicEase .2s forwards

</style>

<script lang="babel">
  import Vue from 'vue'
  import Vuex from 'vuex'

  import { cookie, toNumber, SeqAnimation } from '@bilibili-live/utils'
  import LiveProgress from '@bilibili-live/component.progress-tv'

  import panelConfig from '../config/config.panel-config.js'

  import apis from '../apis'

  import InfoItem from './User-Panel-Info-Item.vue'
  import LevelDetail from './User-Panel-Level-Detail.vue'

  import actions from 'src/vuex/actions'
  import getters from 'src/vuex/getters'
  import store from 'src/vuex/store'

  import userPanelService from '../service/service.user-panel'

  Vue.use(Vuex)

  // Definition: 顺序动画进入方法模块处理对象.
  var panelSeqAnimation = null

  export default {
    data () {
      return {
        initialized: false,
        panelHideTimeout: null,
        loading: true,
        show: false,

        userData: {
          captcha: null,
          username: '神秘用户 (=・ω・=)',
          avatar: '',
          vip: false,  // false || "vip" || "svip"
          rank: {
            level: '--',  // 用户等级.
            rank: '--',  // 用户排名.
            exp: '--',  // 当前等级已有经验值.
            expPercent: 0,  // 升级进度.
            levelExp: '--'  // 当前等级总经验值.
          },
          msgCount: 0
        },

        infoItems: panelConfig.infoItems,  // 面板信息项配置.
        ctrlBtns: panelConfig.ctrlBtns,  // 面板底部按钮项配置.
        seqElements: null  // 顺序进入节点.
      }
    },

    components: { InfoItem, LevelDetail, LiveProgress },

    methods: {
      userPanelIn () {
        userPanelService.showUserPanel()
        this.show = true
      },

      userPanelOut () {
        userPanelService.hideUserPanel(() => {
          this.show = false
        })
      },

      seqIn () {
        // (初始化) 顺序进入.
        if (!panelSeqAnimation) { panelSeqAnimation = new SeqAnimation(this.$els, 'block') }
        panelSeqAnimation.seqIn()
      },

      dataRequest () {
        this.loading = true
        apis.getUserInfo().then(result => {
          result = (typeof result.data === 'string') ? JSON.parse(result.data) : result.data

          if (result.code !== 'REPONSE_OK') {
            return window.console && console.warn(`[TypeError] 用户账号数据返回错误： ${result.code} - ${result.msg}.`)
          }

          var resultData = result.data
          var userData = this.userData

          userData.username = resultData.uname
          userData.avatar = resultData.face

          this.setCurrency({ gold: resultData.gold, silver: resultData.silver, achieve: resultData.achieve })
          this.setAchieve(resultData.achieve)

          userData.rank.level = result.data.user_level
          userData.rank.rank = result.data.user_level_rank
          userData.rank.exp = parseInt(result.data.user_intimacy, 10)
          userData.rank.levelExp = parseInt(result.data.user_next_intimacy, 10)
          userData.rank.expPercent = (userData.rank.exp / userData.rank.levelExp * 100) + '%'

          const isVip = Boolean(parseInt(resultData.vip, 10))
          if (isVip) { userData.vip = 'vip' }

          const isSvip = Boolean(parseInt(resultData.svip, 10))
          if (isSvip) { userData.vip = 'svip' }

          // 如果有 setUserInfo, 设置数据至 Vuex.
          this.setUserInfo && this.setUserInfo({
            username: resultData.uname,
            avatar: resultData.face,

            isVip: resultData.vip,
            isSvip: resultData.svip,

            level: resultData.user_level,
            nextLevel: resultData.user_next_level,

            levelExp: resultData.user_intimacy,
            levelTotalExp: resultData.user_next_intimacy
          })

          this.initialized = true
          this.loading = false
          this.seqIn()
        }).catch(error => {
          window.console && console.error(`[Request Error] 用户账号数据返回错误：${error.statusText}`)
          this.loading = false
        })
      },

      setAvatar () {
        var avatar = this.getAvatarFromCookie()
        if (avatar) {
          this.userData.avatar = avatar
        } else {
          this.dataRequest()
        }
      },

      getAvatarFromCookie () {
        return cookie.getItem('user_face') || ''
      },

      // 获取主站 Captcha.
      getCaptcha () {
        return new Promise((resolve, reject) => {
          if (!window.captcha_key) { apis.getCaptcha().then(callback, callback) }
          function callback () { resolve() }
        })
      },

      // 获取 msgCount.
      getMsgCount () {
        apis.getMsgCount(window.captcha_key).then(msgCountDone.bind(this), msgCountDone.bind(this))
      },

      // 点击消息按钮后回到主窗口时再次检查 msgCount. 方法调取定义在按钮组件的点击指令中.
      msgCountBlurChecking () {
        const self = this
        window.addEventListener('focus', focusChecking)

        function focusChecking () {
          self.getMsgCount()
          window.removeEventListener('focus', focusChecking)
        }
      },

      // 退出登录函数.
      logout () {
        cookie.removeItem('user_face', '/', '.bilibili.com')
      }
    },

    props: ['vipLevel', 'isMaxthon'],

    init () {
      // Action: 检查 ctrlBtns 中是否有 slot 要注册并注册到 partial 中.
      // Partial 为 Vue 的全局 Slot 模板.
      panelConfig.ctrlBtns.forEach((value, index, array) => {
        if (!value.slot) { return }
        Vue.partial(value.slot.name, value.slot.template)
      })
    },

    ready () {
      // Action: 设置头像.
      this.setAvatar()

      // Action: 获取主站验证码并检查 msgCount.
      this.getCaptcha().then(this.getMsgCount)
    },

    store,

    vuex: {
      actions,
      getters
    },

    watch: {
      show (newVal, oldVal) {
        if (newVal) {
          this.initialized ? this.seqIn() : this.dataRequest()
        } else {
          panelSeqAnimation.reset()
        }
      }
    }
  }

  // Definition: 未读提醒回调函数.
  const consolePrefix = 'Live TopNav Panel:'
  function msgCountDone (resultObj) {
    if (resultObj.status !== 200) {
      console.log(`${consolePrefix} 邮件状态获取失败: ${resultObj.status}`)
      return
    }

    const result = resultObj.data
    if (toNumber(result.code) !== 0) {
      console.log(`${consolePrefix} 邮件状态获取失败: code = ${result.code}`)
      return
    }

    var notifyCount = 0
    Object.keys(result.data).forEach(key => { notifyCount += toNumber(result.data[key]) })
    this.userData.msgCount = notifyCount
  }

</script>



