<template lang="jade">
  div.detail-board(:style="data.style")
    div.list(v-for="item in data.data")
      div.item-left.hightlight {{ item.title }}：
      div.item-right
        p(v-for="i in item.detail") {{ i }}
</template>

<style lang="stylus" scoped>
  .hightlight
      color: #edb576
      font-weight: bold
  
  .list
    line-height: 2em

    &:after
      display: block
      content: ''
      clear: both

  .item-left
    float: left
    width: 66px
  
  .item-right
    float: left
</style>

<script>
  export default {
    props: ['data']
  }
</script>


