<template lang="jade">
  div.live-streamer(:class="[type]")
    slot  
    i.ls-left
    i.ls-right
</template>

<style lang="stylus" scoped>
.live-streamer
  position: relative
  margin: 0 auto
  width: 700px
  height: 64px
  border-radius: 4px
  background: rgba(255, 165, 165, 0.1)
  text-align: center
  line-height: 1.75em
  font-size: 14px
  box-sizing: border-box
  color: #cfcdcd

  &.big
    width: 560px
    height: 84px

    .ls-left
    .ls-right
      width: 28px
      height: 66px

      &:after
        top: 33px
    
    .ls-left
      left: -33px

      &:before
      &:after
        left: -20px

      &:before
        border-top: 33px solid rgba(255, 165, 165, 0.1)
        border-left: 20px solid transparent
      &:after
        border-bottom: 33px solid rgba(255, 165, 165, 0.1)
        border-left: 20px solid transparent

    .ls-right
      right: -33px

      &:before
      &:after
        right: -20px

      &:before
        border-top: 33px solid rgba(255, 165, 165, 0.1)
        border-right: 20px solid transparent
      &:after
        border-bottom: 33px solid rgba(255, 165, 165, 0.1)
        border-right: 20px solid transparent              
  
  .ls-left, .ls-right
    position: absolute
    display: block
    top: 34px
    width: 18px
    height: 34px
    background: rgba(255, 165, 165, 0.1)

    &:before, &:after
      position: absolute
      display: block
      content: ''

    &:after
      top: 17px
  
  .ls-left
    left: -23px
    border-radius: 0 2px 2px 0

    &:before
    &:after
      left: -18px

    &:before
      border-top: 17px solid rgba(255, 165, 165, 0.1)
      border-left: 18px solid transparent
    
    &:after
      border-bottom: 17px solid rgba(255, 165, 165, 0.1)
      border-left: 18px solid transparent
  
  .ls-right
    right: -23px
    border-radius: 2px 0 0 2px

    &:before
    &:after
      right: -18px

    &:before
      border-top: 17px solid rgba(255, 165, 165, 0.1)
      border-right: 18px solid transparent
    
    &:after
      border-bottom: 17px solid rgba(255, 165, 165, 0.1)
      border-right: 18px solid transparent

</style>

<script>
  export default {
    props: ['type']
  }
</script>


