<template lang="jade">
  div.content
    div.plate__tab(:class="classObject")
      div(@click="onClickPlateTab(1)") 全部
      div(@click="onClickPlateTab(2)") 大绘马
      div(@click="onClickPlateTab(3)") 小绘马
    div.container__wrap.plate__wrap
      div.container__content
        div.container-cover.top  
        div.container-cover.bottom
        div.plate__box
          template(v-for="plate in wishingLists")
            div.plate__large(v-if="plate.type == 3")
              p.cent {{ plate.content }}
              p.source
                span.id ID: {{ plate.id }}
                span.by by {{ plate.uname }}

            div.plate__medium(v-if="plate.type == 2")
              p.cent {{ plate.content }}
              p.source
                span.id ID: {{ plate.id }}
                span.by by {{ plate.uname }}

            div.plate__small(v-if="plate.type == 1")
              p.id ID：{{ plate.id }}
              p.cent {{ plate.content }}
              p.by by {{ plate.uname }}

</template>

<style lang="stylus" scoped>
@import "../../style/variable.styl"
  .content
    width: 625px
    margin-right: 77px
    &.loaded
      .plate__tab
        > div:nth-child(1)
          background-image: url(../../images/plate__tab-bg1.png)
        > div:nth-child(2)
          background-image: url(../../images/plate__tab-bg2.png)
        > div:nth-child(3)
          background-image: url(../../images/plate__tab-bg3.png)
      .plate__wrap
        .container__content::before
          background-image: url(../../images/bg-light.png)
      .plate__large
          background: url(../../images/plate__large.png)
      .plate__medium
          background: url(../../images/plate__medium.png)
      .plate__small
          background: url(../../images/plate__small.png)

  .plate__tab
    position: relative
    display: flex
    background: linear-gradient(transparent 0%,transparent 50%, #cfb798 80%,#cfb798 100%)
    width: 480px
    margin: 0 auto
    border-top-left-radius: 60px
    border-top-right-radius: 60px
    > div
      display: inline-flex
      box-sizing: border-box
      width: 160px
      height: 60px
      justify-content: center
      align-items: center
      color: #682325
      font-size: 18px
      background: #9c4549
      font-style: oblique
      cursor: pointer
      text-indent: -9999px
      background-repeat: no-repeat
      background-position: center
    > div:nth-child(1)
      border-top-left-radius: 60px
      border-top-right-radius: 5px
    > div:nth-child(2)
      border-left: 1px solid #8a2a2f
      border-right: 1px solid #8a2a2f
      border-top-right-radius: 5px
      border-top-left-radius: 5px
    > div:nth-child(3)
      border-top-left-radius: 5px
      border-top-right-radius: 60px
      
  .plate__tab_current-l
    > div:nth-child(1)
      border-top-right-radius: 0
      background-color: #cfb798
    > div:nth-child(2)
      border-left: none
      border-top-left-radius: 0
      border-bottom-left-radius: 5px
    > div:nth-child(3)
      border-top-left-radius: 5px
      border-top-right-radius: 60px
  .plate__tab_current-c
    > div:nth-child(1)
      border-bottom-right-radius: 5px
    > div:nth-child(2)
      border: none
      background-color: #cfb798
    > div:nth-child(3)
      border-bottom-left-radius: 5px
  .plate__tab_current-r
    > div:nth-child(1)
      border-top-right-radius: 5px
    > div:nth-child(2)
      border-left: 1px solid #8a2a2f
      border-right: none
      border-top-right-radius: 0
      border-bottom-right-radius: 5px
    > div:nth-child(3)
      border-top-left-radius: 0
      background-color: #cfb798

  .container__wrap
    position: relative
    width: 100%
    padding: 3px
    box-sizing: border-box
    background: linear-gradient(to bottom, #cdb896, #776f56)
    border-radius: 20px
    overflow: hidden
  
  .container-cover
    position: absolute
    width: 100%
    height: 50px
    background: none
    pointer-events: none
    z-index: 2

    &.top
      top: 0
      background: linear-gradient(to bottom, #2a191a, #2a191a 40%, rgba(42,25,26,0))
    
    &.bottom
      bottom: 0
      background: linear-gradient(to top, #2a191a, #2a191a 40%, rgba(42,25,26,0))      

  .container__content
    position: relative
    height: 100%
    border-radius: 20px
    background-color: #271413
    overflow: hidden
    createSectionStripe()
  .plate__wrap
    height: 560px
    .container__content::before
      content: ""
      position: absolute
      top: -100px
      left: -38px
      width: 460px
      height: 460px
      background-repeat: no-repeat
      background-size: 100% 100%
      z-index: 0
  .plate__box
    position: relative
    box-sizing: border-box
    height: 95%
    overflow: hidden
    display: flex
    flex-wrap: wrap
    justify-content: space-around
    align-content: flex-start
    margin-top: 15px
    padding: 10px 0 20px 0
    z-index: 1

  .plate__large
      width: 457px
      height: 185px
      padding: 85px 76px 0 76px
      margin-top: 25px
      box-sizing: border-box
      .cent
        font-size: 18px
        font-weight: bold
        color: #5a473e
        text-align: center
      .source
        display: flex
        justify-content: space-between
        margin-top: 24px
        font-size: 16px
      .id
        font-weight: bold
        color: #b5a684
      .by
        color: #7f5a5a

  .plate__medium
    width: 278px
    height: 112px
    padding: 50px 46px 0 46px
    margin-top: 25px
    box-sizing: border-box
    .cent
      font-size: 16px
      font-weight: bold
      color: #5a473e
      text-align: center
    .source
        display: flex
        justify-content: space-between
        margin-top: 13px
        font-size: 12px
      .id
        font-weight: bold
        color: #b5a684
      .by
        color: #7f5a5a

  .plate__small
    width: 182px
    height: 119px
    padding: 30px 18px 0 18px
    margin-top: 25px
    box-sizing: border-box
    font-size: 12px
    .cent
      margin-top: 12px
      color: #5a473e
      text-align: center
    .id
      text-align: center
      font-weight: bold
      color: #b5a684
    .by
      margin-top: 10px
      text-align: right
      color: #5a473e
</style>

<script lang="babel">
  import PerfectScrollbar from 'perfect-scrollbar'
  import '../../../../node_modules/perfect-scrollbar/dist/css/perfect-scrollbar.css'
  import { GetWishingList } from '../../service'
  import Toast from '../toast'
  export default {
    data () {
      return {
        tabId: 1,
        wishingLists: []
      }
    },
    computed: {
      classObject: function () {
        return {
          'plate__tab_current-l': this.tabId === 1,
          'plate__tab_current-c': this.tabId === 2,
          'plate__tab_current-r': this.tabId === 3
        }
      }
    },
    methods: {
      onClickPlateTab (id) {
        this.tabId = id
        const type = (function () {
          if (id === 2) {
            return 2
          }
          if (id === 3) {
            return 1
          }
          return ''
        })()
        GetWishingList(type)
        .then(res => {
          this.wishingLists = res
          const container = document.querySelector('.plate__box')
          container.scrollTop = 0
          PerfectScrollbar.update(container)
        })
        .catch(err => {
          Toast(err.json().msg)
        })
      }
    },
    ready () {
      this.$nextTick(() => {
        PerfectScrollbar.initialize(document.querySelector('.plate__box'))
      })
      GetWishingList('')
      .then(res => {
        this.wishingLists = res
      })
      .catch(err => {
        Toast(err.json().msg)
      })
    }
  }
</script>


