<!--
    Live Navbar Top By LancerComet at 14:15, 2016.07.07.
    # Carry Your World #
    ---
    直播通用顶栏.

    组件参数:
     - logoLink: String, 点击 Logo 时的跳转目标.
-->

<template lang="jade">
  nav.live-navbar-top.p-relative
    div.main-ctnr.h-100.m-auto.f-clear

      //- 左侧部分.
      div.left-part.f-left.p-relative

        //- Logo.
        a.v-bottom(:href="logoLink")
          div.nav-logo.dp-i-block.v-bottom(role="img")

        //- 导航项.
        div.nav-items-ctnr.dp-i-block.v-bottom
          nav-item(v-for="item in navItems", :href="item.href", :text="item.text", :target="item.target", :drawer="item.drawer", :mouse-enter="item.mouseEnter", :mouse-leave="item.mouseLeave", :is-maxthon="isMaxthon", :arrow-up="item.arrowUp", :arrow-up-now="arrowUp")

        //- 分区导航面板.
        area-nav-panel.area-nav-panel(:show="showAreaNav", :is-maxthon="isMaxthon")

        //- 导航面板.
        download(:show="showDownload", :is-maxthon="isMaxthon")

      //- 右侧部分.
      div.right-part.h-100.f-right.f-clear
        search-bar.f-left
        user-doms.f-left(v-if="loggedIn", :is-maxthon="isMaxthon")
        guest-doms.f-left(v-if="!loggedIn")

    live-cert-iframe(v-if="showCertIframe")

</template>

<style lang="stylus">
  @import "~@bilibili-live/utils/style/variable.styl"

  $topNavSize = {
    height: 50px,
    minWidth: 1180px
  }

  $logoSize = {
    width: 150px,
    height: $topNavSize.height
  }

  // Standard Style.
  .live-navbar-top
    min-width: $topNavSize.minWidth
    height: $topNavSize.height
    background-color: #fff
    box-shadow: 0 0 5px 1px rgba(0,0,0,.5)
    z-index: 1

    .main-ctnr
      width: 90%
      max-width: 1143px

      .nav-logo
        width: $logoSize.width
        height: $logoSize.height
        background-image: url("./assets/live-logo-150-cyan.png")

      .area-nav-panel
        left: 136px

</style>

<script lang="babel">
  import { loginStatus } from '@bilibili-live/utils'

  import NavItem from './child-component/Nav-Item.vue'
  import AreaNavPanel from './child-component/Area-Nav-Panel.vue'
  import Download from './child-component/Download/Download.vue'
  import SearchBar from './child-component/Search-Bar.vue'
  import GuestDoms from './child-component/Guest-Doms.vue'
  import UserDoms from './child-component/User-Doms.vue'
  import LiveCertIframe from './child-component/Cert-Popup/Live-Cert-iframe.vue'

  import navItems from './config/config.nav-items.js'
  import areaPanelService from './service/service.area-panel.js'
  import downloadPanelService from './service/service.download-panel.js'

  import EventBus from 'src/event-bus'
  import eventName from './config/config.event-name'

  var arrowUpTimeout = null

  export default {
    data () {
      return {
        showCertIframe: false,  // 认证 iframe 控制标识.
        loggedIn: false,  // 登录状态.
        showAreaNav: false,
        showDownload: false,
        arrowUp: '',
        isMaxthon: !!window.navigator.userAgent.match(/Maxthon\/4.[0-4]/)  // 为 Maxthon 4.4 添加额外鼠标事件.
      }
    },

    props: {
      logoLink: {
        type: String,
        default: '/'
      },
      navItems: {
        type: Array,
        default: navItems
      }
    },

    components: { NavItem, AreaNavPanel, Download, SearchBar, GuestDoms, UserDoms, LiveCertIframe },

    methods: {
      setArrowUp (target) {
        clearTimeout(arrowUpTimeout)
        this.arrowUp = target
      },

      restoreArrowUp () {
        arrowUpTimeout = setTimeout(() => {
          this.arrowUp = ''
        }, 250)
      },

      setLoginStatus () {
        this.loggedIn = loginStatus()
      },

      /**
       *  注册面板控制事件.
       */
      registerEvents () {
        // 显示分区导航面板开启事件.
        EventBus.$on(eventName.showAreaNavPanel, () => {
          areaPanelService.showAreaNav()
          this.showAreaNav = true
          this.setArrowUp('areaNavPanel')
        })

        // 关闭分区导航面板开启事件.
        EventBus.$on(eventName.hideAreaNavPanel, () => {
          this.restoreArrowUp()
          areaPanelService.hideAreaNav(() => {
            this.showAreaNav = false
          })
        })

        // 显示直播姬下载面板.
        EventBus.$on(eventName.showDownloadPanel, () => {
          downloadPanelService.showPanel()
          this.showDownload = true
          this.setArrowUp('download')
        })

        // 隐藏直播姬下载面板.
        EventBus.$on(eventName.hideDownloadPanel, () => {
          this.restoreArrowUp()
          downloadPanelService.hidePanel(() => {
            this.showDownload = false
          })
        })

        // 认证弹窗 iFrame 显示事件.
        EventBus.$on(eventName.showCertIframe, () => {
          this.showCertIframe = true
        })
      }
    },

    ready () {
      this.setLoginStatus()
      this.registerEvents()
    }
  }
</script>



