<template lang="jade">
  div.user-level-detail.dp-none.p-absolute.t-left.a-move-in-right(:class="theme")
    div.darkgray(style="margin-bottom: 15px")
      p(style="margin-top: 0") 当前等级：UL.{{level}}
      p 当前排名：{{rank}}
      p 升级还需经验：{{expLeft}}

    hr

    div.lightgray(style="margin-top: 10px")
      div(style="margin-bottom: 8px; text-indent: 1em; line-height: 20px")
        p 1. 经验值通过投喂道具、观看直播时长提升。
        p 2. 每 1 个瓜子提升1点经验，每 5 分钟提升 3000 点经验。
        p 3. 老爷身份，在观看直播时经验翻 1 倍。

    hr

    a(href="/news/detail/52", target="_blank")
      div.detail-link.t-center.ts-dot-2(role="button") 查看等级说明

</template>

<style lang="stylus" scoped>
  @import "~@bilibili-live/utils/style/variable.styl"

  $userLevelPanel = {
    width: 220px
  }

  .darkgray
    color: #333

  .lightgray
    color: #aeaeae

  hr
    border: 0
    border-bottom: 1px solid #ccc

  .user-level-detail
    width: $userLevelPanel.width
    padding: 15px 15px 10px 15px
    margin-right: 10px
    right: 100%
    top: -50px
    border: 1px solid #ddd
    border-radius: 5px
    background-color: #fff
    font-size: 12px
    color: $biliColor.gray
    box-shadow: 0 0 2em .1em rgba(0,0,0,.2)

    &:before
    &:after
      content: ""
      display: block
      position: absolute

    &:before
      width: 20px
      height: 100%
      top: 0
      left: 100%

    &:after
      top: 59px
      right: -20px
      border: 10px solid #fff
      border-top-color: transparent
      border-bottom-color: transparent
      border-right-color: transparent

    p
      margin: 5px 0

    .detail-link
      height: 40px
      border-radius: 3px
      font-size: 13px
      line-height: 40px
      color: $biliColor.cyan

      &:hover
        background-color: #eaecf1

      &:active
        background-color: $biliColor.cyan
        color: #fff

</style>

<script lang="babel">
    export default {
      props: ['level', 'rank', 'expLeft']
    }
</script>



