import Vue from 'vue'
import VueResource from '@bilibili-live/vue-resource'

Vue.use(VueResource)

export default {
  getUserRank,        // 获取用户数据
  getMasterRank,     // 按照
  getUserRankDate,   // 按日期获取数据
  getMasterRankDate   // 按日期获取数据
}

var urls = {
  master: '/eventSubject/masterRank',
  user: '/eventSubject/userRank',
  userDate: '/eventSubject/userRankDate',
  masterDate: '/eventSubject/masterRankDate'
}

function getUserRankDate (param) {
  var url = urls.userDate

  return Vue.http.get(url + '?' + getParamStr(param))
}

function getMasterRankDate (param) {
  var url = urls.masterDate

  return Vue.http.get(url + '?' + getParamStr(param))
}

/*
 *  获取用户总榜
 */
function getUserRank (param) {
  var url = urls.user

  return Vue.http.get(url + '?' + getParamStr(param))
}

/*
*  获取主播榜
*/
function getMasterRank (param) {
  var url = urls.master

  return Vue.http.get(url + '?' + getParamStr(param))
}

function getParamStr (obj) {
  let str = ''

  for (let o of Object.keys(obj)) {
    str += obj[o] ? ('&' + o + '=' + obj[o]) : ''
  }

  // console.log(str)

  return str.substring(1)
}



