<!--
    Live-Popup By LancerComet at 11:24, 2016/7/19.
    # Carry Your World #
    ---
    # Live Popup 组件.

    ## 创建弹窗:
    ```javascript
    /*-- 引入 Live Popup. --*/
    import livePopup from 'live-popup'
    Vue.use(livePopup)
    ```

    ```html
    /*-- 在页面最底部插入标签 --*/
    <body>
      ...
      <live-popup></live-popup>
    </body>
    ```

    ```javascript
    /* -- 使用 Live Popup. -- */
    // 创建弹窗.
    this.newPopup({
        title: String                          // 弹窗标题.
        titleCenter: Boolean[false],           // 标题是否居中, 默认为 false.
        content: String,                       // 弹窗使用的提示文本字符串. 当使用 html 时, content 字段将失效.
        html: String,                          // 弹窗使用的 html 片段. 当使用 html 时, content 字段将失效.
        partial: String,                       // 弹窗使用的 partial 名称. 请注意, 在使用 partial 时请不要传入 content 与 html 字段.
        lockWhenConfirm: Boolean[false]        // 点击确认时是否锁定弹窗. 解锁需要手动调取 this.unlock() 解锁, 默认为 false.
    })

    // 弹窗在创建前执行的回调函数.
    .beforeInit(fucntion () {
        //...
    })

    // 弹窗创建完毕后执行的回调函数.
    .onInited(function () {
        // 函数内可使用参数:
        this.$id        // 弹窗 ID, 同时也是弹窗内容节点的 html id.
        this.html       // 弹窗内容 HTML 字符串.
    })

    // 点击确认按钮时执行的回调函数. 当不传入时, 将直接关闭弹窗.
    .onConfirm(function () {
        // 函数内可调用方法:
        this.lock()         // 锁定弹窗.
        this.unlock()       // 解锁弹窗.
        this.close()        // 关闭弹窗.
        this.keep()         // 标记弹窗为保存模式.
        this.cancelKeep()   // 取消弹窗的保存模式.
    })

    // 弹窗创建时执行的回调函数.
    .onCancel(function () {
        // ...
    })
    ```

-->

<template lang="jade">
  div.live-popup-ctnr.dp-table.w-100.h-100.p-fixed.p-zero.f-family(v-if="popups.length", v-show="popups.length", transition="out", role="alertdialog")
    //- Body Merge.
    div.body-merge.w-100.h-100.p-absolute.p-zero

    //- Live Popup Panel Container.
    div.dp-table-cell.v-middle

      //- Live Popup Panel 弹窗节点.
      div.live-popup-panel.p-relative.m-auto.a-move-in-bottom.a-forwards(:class="{'a-scale-out': currentPopup.$out}", :style="{'width': currentPopup.width + 'px' || 500}", :init="init(currentPopup)")

        //- 标题节点.
        div.title-ctnr.p-relative(:class="{'t-center': currentPopup.titleCenter}")
          h2.popup-title(v-text="currentPopup.title")

        //- 内容节点.
        div.popup-content-ctnr(v-html="currentPopup.html !== undefined ? currentPopup.html : (currentPopup.content !== undefined ? '<p>' + currentPopup.content + '</p>' : '<p></p>')", :id="currentPopup.$id")
          partial(:name="currentPopup.partial")

        //- 按钮节点.
        div.popup-btn-ctnr.t-center(v-if="currentPopup.button !== false")
          button.live-btn.default.confirm(v-text="confirmBtnText", @click="confirm(currentPopup)", :class="{'disabled': currentPopup.$disabled}", role="button", v-el:confirm-btn)
          button.live-btn.default.cancel(v-if="currentPopup.type !== 'info'", v-text="cancelBtnText", @click="cancel(currentPopup)", :class="{'disabled': currentPopup.$disabled}", role="button", v-el:cancel-btn)

        //- 关闭按钮.
        div.close-btn.p-absolute.bg-center.bg-no-repeat.b-circle.pointer(role="button", title="关闭面板", @click="cancel(currentPopup)", :class="{'disabled': currentPopup.$disabled}", v-el:close-btn)

</template>

<style lang="stylus">
  @import '~@bilibili-live/utils/style/variable.styl';
  @import '~@bilibili-live/component.button/index.styl';

  $popup = {
    width: 500px,
    padding: 20px,
    radius: 5px,
    shadow: 0 0 5em .5em rgba(0,0,0,0.2)
  }

  .live-popup-ctnr
    font-size: 12px
    z-index: 10000

    &.out-leave
      animation: fade-out $cubicEase .4s

    p
      margin: 15px 0

    .body-merge
      background-color: rgba(0,0,0,.5)

    .live-popup-panel
      width: $popup.width
      height: auto
      top: 50%
      padding: $popup.padding
      border-radius: $popup.radius
      background-color: #fff
      box-shadow: $popup.shadow
      word-wrap: break-word
      word-break: break-word

      &.a-scale-out
        animation-fill-mode: forwards

      .title-ctnr
        padding-bottom: 5px

        .popup-title
          margin: 0
          color: $biliColor.cyan
          font-weight: 100
          font-size: 20px

      .popup-btn-ctnr
        .live-btn
          margin: 0 10px

      .close-btn
        background-image: url('./assets/close-btn.png')
        background-color: $biliColor.cyan
        width: 30px
        height: 30px
        right: -20px
        top: -20px
        border: 5px solid #fff
        color: #fff
        font-size: 20px
        font-family: cursive
        text-align: center
        line-height: 30px
        transition: all $cubicEase .3s

        &:hover
          transform: rotate(180deg) scale(1.1)

        &:active
          transform: rotate(180deg) scale(1)
          transition: none !important

        &.disabled
          background-color: $biliColor.lightgray
          cursor: not-allowed
          &:hover
          &:active
            transform: rotate(0) scale(1) !important

</style>

<script lang="babel">
  import popups from './services/service.popups-store.js'

  // Definition: 弹窗组件定义.
  export default {
    data () {
      return {
        popups: popups,  // 弹窗存放数组定义.
        currentPopup: {}  // 当前使用弹窗.
      }
    },

    computed: {
      confirmBtnText () {
        if (this.currentPopup.button) {
          return this.currentPopup.button.confirm ? this.currentPopup.button.confirm : '确定'
        } else {
          return '确定'
        }
      },

      cancelBtnText () {
        if (this.currentPopup.button) {
          return this.currentPopup.button.cancel ? this.currentPopup.button.cancel : '取消'
        } else {
          return '取消'
        }
      }
    },

    methods: {
      init (popupObj) {
        popupObj.onInited && setTimeout(() => popupObj.onInited(popupObj), 10)
        return true
      },

      // 点击事件：确定.
      confirm (popupObj) {
        // 弹窗锁定时 return.
        if (popupObj.$disabled) { return }

        // 置顶锁定时锁定弹窗.
        popupObj.lockWhenConfirm && popupObj.lock()

        // 将 popupObj 加入 button 节点引用.
        if (!popupObj.confirmBtn) {
          popupObj.confirmBtn = this.$els.confirmBtn
        }

        if (!popupObj.cancelBtn) {
          popupObj.cancelBtn = this.$els.cancelBtn
        }

        if (!popupObj.closeBtn) {
          popupObj.closeBtn = this.$els.closeBtn
        }

        // 有传入 onConfirm 时执行传入函数.
        // 执行完毕后调用 done 向上通知.
        if (popupObj.onConfirm) {
          popupObj.onConfirm(popupObj)
          return
        }

        // 没有传入 onConfirm 时直接移除弹窗.
        popupObj.removePopup()
      },

      // 点击事件：取消.
      cancel (popupObj) {
        if (popupObj.$disabled) { return }

        // 将 popupObj 加入 button 节点引用.
        if (!popupObj.confirmBtn) {
          popupObj.confirmBtn = this.$els.confirmBtn
        }

        if (!popupObj.cancelBtn) {
          popupObj.cancelBtn = this.$els.cancelBtn
        }

        if (!popupObj.closeBtn) {
          popupObj.closeBtn = this.$els.closeBtn
        }

        popupObj.onCancel && popupObj.onCancel(popupObj)
        popupObj.removePopup()
      },

      // 关闭弹窗.
      close (popupObj) {
        popupObj.removePopup()
      }
    },

    watch: {
      popups: function (newVal, oldVal) {
        this.currentPopup = newVal[newVal.length - 1]  // 监视 popups 并取出最后一个 Popup 放至在 currentPopup.
      }
    }
  }
</script>



