import { AREALIST } from '../mutation-types'

const iconPrefix = 'live-icon-16 area '
const state = {
  subject: {
    skipIndex: true, iconName: iconPrefix + 'subject', text: '推荐主播', href: '/subject'
  },

  hot: {
    skipNav: true, text: '热门直播', href: '/all'
  },

  draw: {
    iconName: iconPrefix + 'draw', text: '绘画专区', href: '/draw'
  },

  'sing-dance': {
    iconName: iconPrefix + 'sing-dance', text: '唱见舞见', href: '/sing-dance'
  },

  otaku: {
    iconName: iconPrefix + 'otaku', text: '御宅文化', href: '/otaku'
  },

  'ent-life': {
    iconName: iconPrefix + 'ent-life', text: '生活娱乐', href: '/ent-life'
  },

  single: {
    iconName: iconPrefix + 'single', text: '单机联机', href: '/single'
  },

  online: {
    iconName: iconPrefix + 'online', text: '网络游戏', href: '/online'
  },

  'e-sports': {
    iconName: iconPrefix + 'e-sports', text: '电子竞技', href: '/e-sports'
  },

  mobile: {
    iconName: iconPrefix + 'mobile', text: '手机直播', href: '/mobile'
  },

  'mobile-game': {
    iconName: iconPrefix + 'mobile-game', text: '手游直播', href: '/mobile-game'
  },

  movie: {
    iconName: iconPrefix + 'movie', text: '放映厅', href: '/movie'
  }
}

const mutations = {
  [AREALIST] (state, areaList) {
    state.areaList = areaList
  }
}

export default { state, mutations }



