import Vue from 'vue'
import VueResource from 'vue-resource'

function catchError (e) {
  console.log('THE SERVER RETURNS AN ERROR:')
  console.log(`STATUS: ${e.status}, DETAIL: ${e.statusText}`)
}

if (!Vue.http) { Vue.use(VueResource) }

export function Get (url) {
  return new Promise((resolve, reject) => {
    Vue.http.get(url).then(res => {
      if (!res.ok) {
        catchError(res)
        reject(res)
        return
      }

      const result = res.json()
      if (result.code !== 0) {
        catchError({ status: result.code, statusText: result.msg })
        reject(res)
        return
      }

      resolve(result.data)
    }, xhrObject => {
      catchError(xhrObject)
      reject(xhrObject)
    })
  })
}

export function Post (url, data) {
  return new Promise((resolve, reject) => {
    Vue.http.post(url, data, {emulateJSON: true}).then(res => {
      if (!res.ok) {
        catchError(res)
        reject(res)
        return
      }

      const result = res.json()
      if (result.code !== 0) {
        catchError({ status: result.code, statusText: result.msg })
        reject(res)
        return
      }

      resolve(result.data)
    }, xhrObject => {
      catchError(xhrObject)
      reject(xhrObject)
    })
  })
}



