<template lang="jade">
  div.rank-area(name='posRankArea')
    section-title(:index='5')
    Live-activity-rank(:data='rankData' v-if="loaded")
    p.rank-tip 注：本排行榜只展示1个头衔。有多个头衔的时候，默认展示最高等级头衔。
</template>

<style lang="stylus" scoped>
  .rank-area
    position: relative
    margin-top: 160px
    height: 950px
    z-index: 1

    .section-title
      margin-bottom: 40px
    
    .rank-tip
      text-align: center
      color: rgba(255, 255, 255, .9)
      margin-top: 40px
</style>

<script>
  import SectionTitle from '../section-title'
  import LiveActivityRank from '../../../_common/components/Live-Activity-Rank/Live-Activity-Rank.vue'

  export default {
    data () {
      return {
        rankData: {
          type: 'desktop',
          eventKey: 'lastorder',
          className: 'lastorder',
          cache: true,
          first: 'master:last',
          tabs: [{
            rankType: 'master',
            type: 'master:last',
            text: '主播榜'
          }, {
            rankType: 'user',
            type: 'user:last',
            text: '用户榜'
          }],
          subTabs: {
            'user:last': {
              rankType: 'date',
              tabs: [{
                type: 'total',
                text: '总榜'
              }, {
                type: 'today',
                text: '今日榜'
              }, {
                type: 'yesterday',
                text: '昨日榜'
              }]
            }
          }
        }
      }
    },
    components: {
      SectionTitle,
      LiveActivityRank
    },
    props: ['loaded']
  }
</script>


