export default function () {
  // DOM
  let loadingScreen = document.querySelector('.loading-screen')
  let progress = document.querySelector('.ls-progress')

  // DATA
  let Resource = [
    require('../../images/md-ball.png'),
    require('../../images/star1.png'),
    require('../../images/star2.png'),
    require('../../images/star3.png'),
    require('../../images/star4.png'),
    require('../../images/star5.png'),
    require('../../images/star6.png'),
    require('../../images/s22.png'),
    require('../../images/s33.png'),
    require('../../images/base-a-1.png'),
    require('../../images/base-a-2.png'),
    require('../../images/base-d-1.png'),
    require('../../images/base-d-2.png'),
    require('../../images/base-e-1.png'),
    require('../../images/base-e-2.png'),
    require('../../images/base-e-3.png'),
    require('../../images/base-e-4.png'),
    require('../../images/base-l.png'),
    require('../../images/basel-a.png'),
    require('../../images/basel-d-1.png'),
    require('../../images/basel-l.png'),
    require('../../images/basel-o-1.png'),
    require('../../images/basel-o-2.png'),
    require('../../images/basel-r-1.png'),
    require('../../images/basel-rr.png'),
    require('../../images/basel-s.png'),
    require('../../images/basel-t.png'),
    require('../../images/base-o-1.png'),
    require('../../images/base-r-1.png'),
    require('../../images/base-r-2.png'),
    require('../../images/base-r-3.png'),
    require('../../images/base-rr-1.png'),
    require('../../images/base-rr-2.png'),
    require('../../images/base-rr-3.png'),
    require('../../images/base-s-1.png'),
    require('../../images/base-s-2.png'),
    require('../../images/base-t-1.png'),
    require('../../images/base-t-2.png'),
    require('../../images/colorful-tie.png'),
    require('../../images/subtitle.png'),
    require('../../images/sidebar_star.png'),
    require('../../images/bl_small.png'),
    require('../../images/bl_big.png'),
    require('../../images/bl-purple.png'),
    require('../../images/kanban_left.png'),
    require('../../images/kanban_right.png'),
    require('../../images/shine.png')
  ]
  let len = Resource.length
  let loaded = 0

  // LOGIC
  return new Promise((resolve, reject) => {
    // Resource = []
    Resource.forEach(r => {
      let img = new Image()
      img.onload = function () {
        loaded++
        progress.innerHTML = Math.round(loaded / len * 100) + '%'
        if (loaded === len) {
          console.log('RESOURCE LOADED:', len)
          loadingScreen.className += ' hide'
          setTimeout(() => {
            document.body.removeChild(loadingScreen)
          }, 1500)
          resolve()
        }
      }
      img.src = r
    })
  })
}



