<template lang="jade">
  div.search-bar-ctnr.h-100
    form(action="/search/index/", name="search-form", target="_blank", @submit.prevent, v-el:form)
      div.search-bar(:class="{'search-bar-focus': focus}")
        input(type="text", name="keyword", title="搜索主播或直播间", aria-label="搜索主播或直播间", placeholder="搜索主播或直播间", maxlength="50", v-el:search-input, v-model="keyword", @focus="focusAction()", @blur="blurAction()")
        button.search-btn.v-middle.pointer(type="submit", aria-label="搜索主播或直播间", title="_(:3 」∠)_", role="search", @click="search")
          i.icon-font.icon-search
</template>

<style lang="stylus">
  @import "~@bilibili-live/utils/style/variable.styl"

  $searchBar = {
    height: 30px
    color: #c7ccd4
  }

  .search-bar-ctnr
    margin-right: 20px

    .search-bar
      height: $searchBar.height
      margin: 10px 0
      border-bottom: 1px solid $searchBar.color

      input
        width: 150px
        height: $searchBar.height
        padding: 0 5px
        background-color: transparent
        border: 0
        outline: none

        &::placeholder
          color: $searchBar.color

      .search-btn
        background-color: transparent
        border: 0
        transform: scale(1)
        color: $biliColor.lightgray

        &:hover
          color: $biliColor.cyan

        &:active
          transform: scale(.8)
          transition: none

    .search-bar-focus
      border-bottom: 2px solid $biliColor.cyan

      .search-btn
        color: $biliColor.cyan

</style>

<script lang="babel">
  import Vue from 'vue'
  import LiveToast from '@bilibili-live/component.toast'
  import { randomEmoji } from '@bilibili-live/utils'

  Vue.use(LiveToast)

  export default {
    data () {
      return {
        keyword: '',
        focus: false
      }
    },

    methods: {
      focusAction: function () {
        this.focus = true
      },
      blurAction: function () {
        this.focus = false
      },
      search: function () {
        if (!this.keyword.length) {
          this.liveToast(this.$els.searchInput, `帅的人写了内容，丑的人还再尝试 ${randomEmoji.helpless()}`)
          return
        }
        if (this.keyword.replace(/[^\x00-\xff]/g, "aa").length > 50) {
          this.liveToast(this.$els.searchInput, `汉字不能超过 25 个字喔 ${randomEmoji.helpless()}`)
          return
        }
        this.$els.form.submit()
      }
    }
  }
</script>



