    ---
    Live Progress 组件.
-->

<template lang="jade">
  div.progress-tv.w-100.h-100.p-absolute.p-zero(role="progress", v-show="show", transition="fade", :style="{'background-color': backgroundColor}")
    div.progress-img.bg-no-repeat.bg-center.p-center
</template>

<style lang="stylus" scoped>
  .progress-tv
    background-color: #fff
    z-index: 10

    .progress-img
      background-image: url("./asserts/progress-tv.gif")

</style>

<script>
  export default {
    props: {
      show: {
        default: false
      },

      backgroundColor: {
        type: String,
        default: '#fff'
      }
    },
  }
</script>



