<template lang="jade">
  div.user-panel.v-top.p-relative.f-clear

    div.user-panel-ctnr.f-left
      user-panel(:show="showUserPanel", :is-maxthon="isMaxthon")
    
    div.user-live-ctnr.f-left.f-clear
      enter-room(@click="enterMyRoom")
      user-guide
</template>

<style lang="stylus" scoped>
  @import "~@bilibili-live/utils/style/variable.styl";

  $topNavHeight = 50px

  $roomEnterBtnSize = {
    height: 40px
  }

  .user-panel

    .user-panel-ctnr
      width: 100px
      height: $topNavHeight
      right: $roomEnterBtnSize.width

    .user-live-ctnr 
      margin-top: 11px 
      width: 122px 
      height: 32px
      box-sizing: border-box
      box-shadow: 0px 2px 5px rgba(34,23,21,0)
      overflow: hidden
      background-color: #fff 
      border-radius: 2px
      &:hover 
        height: 64px
        transition: all ease-in .3s
        box-shadow: 0px 2px 5px rgba(34,23,21,.2)
        .room-enter-btn
          color: #999
          &:hover 
            color: #23aee6

</style>

<script>
  import apis from '../apis'

  import UserPanel from './User-Panel.vue'
  import { liveQuickLogin, toNumber } from '@bilibili-live/utils'
  import liveCertPopups from './Cert-Popup'
  import EnterRoom from './Enter-Room.vue'
  import UserGuide from './user-guide.vue'

  // Definition: 房间进入回调逻辑.
  const enterMyRoomCallbacks = {
    cellphoneBinding: function () {
      liveCertPopups.cellphoneBinding()
    },

    enterRoom: function (roomID) {
      window.location.href = '/' + roomID
    },

    realnameCert: function () {
      this.$dispatch('show-live-cert-iframe')
    },

    failure: function (xhrObject) {
      liveCertPopups.failure(xhrObject)
    }
  }

  export default {
    data () {
      return {
        showUserPanel: false
      }
    },

    components: { UserPanel, EnterRoom, UserGuide },

    props: ['isMaxthon'],

    methods: {
      enterMyRoom () {
        liveQuickLogin()
        apis.enterMyRoom().then(res => {
          var result = JSON.parse(res.data)

          // 需要手机绑定提示.
          if (toNumber(result.code) === -800) {
            enterMyRoomCallbacks.cellphoneBinding()
            return
          }

          // 进入直播间 & 实名认证.
          if (toNumber(result.code) === 0) {
            // 进入直播间 || 需要实名认证.
            result.data ? enterMyRoomCallbacks.enterRoom(result.data) : enterMyRoomCallbacks.realnameCert.call(this)
          }
        }, enterMyRoomCallbacks.failure)
      }
    }

  }

</script>



