import { Post } from './srv.http'

export default function (type, content) {
  return new Promise((resolve, reject) => {
    Post('/lastOrder/wish', {
      type: type,
      content: content
    }).then(data => {
      resolve(data)
    }).catch(xhrObject => reject(xhrObject))
  })
}



