<!--
    Rank-List By LancerComet at 18:39, 2016/7/21.
    # Carry Your World #
-->

<template lang="jade">
  div.activity-rank-component(:class="className", v-bind:class="{ 'mobile': type === 'mobile', 'desktop': type === 'desktop' }")
    div.tabs-box
      div.tabs
        ul.tab-list(v-bind:class="{  'one-tab': tabs.length === 1, 'three-tab': tabs.length === 3 }")
          li.item(v-for="tab in tabs", @click="changeTab(tab)", v-bind:class="{ 'active': tab.type === curTab.type }")
            p.tab-text
              | {{{tab.text}}}

      div.sub-tabs(v-if="curSubTab")
        ul.tab-list(v-for="(superKey, subValue) in subTabs", v-show="superKey === curTab.type")
          li.item(v-for="tab in subValue.tabs", @click="changeSubTab(tab)", v-bind:class="{ 'active': tab.type === curSubTab.type && superKey === curTab.type}")
            p.tab-text
              | {{{tab.text}}}

    div.list-box
      div.list-box-cntr.rank-list-ctnr(v-el:rank-list-ctnr)
        ul.rank-list.rank-list-main(v-el:rank-list-main)
          li.item(v-for="li in listNowData")
            span.num
              | {{li.rank}}
            span.face
              a(:href="li.link", target="_blank", v-if="curTab.rankType === 'master' ")
                img(:src='li.face | changeFace')
              img(:src='li.face', v-else)
            span.uname.t-over-hidden(v-if="curTab.rankType === 'master' ")
              a(:href="li.link", target="_blank")
                | {{li.uname}}
            span.uname.t-over-hidden(v-else)
              | {{li.uname}}
            span.title-area
              span.title(:class="li.title")
            span.icon
              <i class="score-icon score-special-{{ li.score_pic || 0 }} " ></i>
            span.score
              | {{li.score}}
        p.loading.t-center(v-show="isRequestIng")
          | 载入中……
      div.user-info
        ul.rank-list(v-if="curUserData")
          li.item
            span.num
              | {{curUserData.rank}}
            span.face
              img(:src='curUserData.face | changeFace')
            span.uname.t-over-hidden
              | {{curUserData.uname}}
            span.title-area
              span.title(:class="curUserData.title")
            span.icon
              <i class="score-icon score-special-{{ curUserData.score_pic || 0 }} " ></i>
            span.score
              | {{curUserData.score}}

        p.no-login-tips(v-if="!curUserData")
          span.btn-login(@click="doLogin") 登录
          | 后才可以查看排名哦~
</template>

<style lang="stylus" scoped>
  @import "../../../../node_modules/perfect-scrollbar/dist/css/perfect-scrollbar.css";
  @import "./mobile.styl";
  @import "./desktop.styl";
</style>

<script lang="babel">
  'use strict'
  import VueResource from '@bilibili-live/vue-resource'
  import perfectScrollbar from 'perfect-scrollbar'
  import Vue from 'vue'
  import RequestService from './services/service.js'
  import { liveQuickLogin, bfsImgLink } from '@bilibili-live/utils'

  Vue.use(VueResource)

  export default {
    data () {
      return {
        rankName: '',     // 排行榜组件唯一标识
        type: '',         // mobile 移动版 desktop pc web
        eventKey: '',     // 活动eventkey
        className: '',    // 类名
        cache: true,      // 是否缓存数据 默认缓存
        first: '',        // 默认定位tab type
        tabs: [],         // 主Tab
        subTabs: {},      // 子Tab
        listData: [],       // 列表数据数组
        listNowData: null,    // 当前列表数据
        curUserData: null,    // 当前用户的数据
        curTab: {},       // 当前Tab
        curSubTab: {},     // 当前子tab
        isRequestIng: false,
        changeSuperTab: null  // 外界修改
      }
    },

    components: {},

    props: ['data'],
    events: {
      'rank:changeSuperTab' (rankName, tab) {
        if (rankName !== this.rankName) {
          return
        }
        this.changeTab(tab)
      }
    },
    methods: {
      doLogin () {
        liveQuickLogin()
      },
      filterFace (src) {
        return bfsImgLink(src)
      },
      changeTab (tab) {
        let sb = this.subTabs[tab.type]

        this.curTab = tab
        this.curSubTab = sb && sb.tabs[0]

        this.changeTabData()
      },

      changeSubTab (subTab) {
        this.curSubTab = subTab
        this.changeTabData()
      },

      changeTabData () {
        let nowList = this.getNowList()

        this.listNowData = nowList.list

        this.curUserData = Object.prototype.toString.call(nowList.info) === '[object Array]' ? '' : nowList.info
        this.scrollToTop()
        if (this.listNowData.length === 0) {
          this.update()
        } else {
          this.updateListView()
        }
      },

      setFirstTab () {
        this.tabs.forEach((v, i) => {
          if (v.type === this.data.first) {
            this.changeTab(v)
            return
          }
        })
      },

      // 切换排行榜函数.
      changeList (listObj) {
        this.curTab = listObj
      },

      // 滚动至最底部.
      scrollToBottom () {
        this.$els.rankListCtnr.scrollTop = this.$els.rankListCtnr.scrollHeight
      },
      // 滚动至最底顶部.
      scrollToTop () {
        this.$els.rankListCtnr.scrollTop = 0
      },

      // 获取数据.
      update (page = 1) {
        if (this.isRequestIng) {
          return
        }

        this.isRequestIng = true

        let param = {}
        let func = ''
        let eventKey = this.data.eventKey       // 活动关键字
        let isHasSubTab = this.curSubTab
        let superType = this.curTab.type
        let subType = isHasSubTab ? this.curSubTab.type : ''
        let superRankType = this.curTab.rankType
        let subRankType = isHasSubTab ? this.subTabs[superType].rankType : ''

        if (superType.indexOf(':') > -1) {
          superType = superType.split(':')[1]
        }
        if (isHasSubTab) {
          if (subRankType === 'date') {
            func = superRankType === 'master' ? 'getMasterRankDate' : 'getUserRankDate'
          } else {
            func = superRankType === 'master' ? 'getMasterRank' : 'getUserRank'
          }
        } else {
          func = superRankType === 'master' ? 'getMasterRank' : 'getUserRank'
        }

        param = {
          page,
          eventKey,
          superRankType,
          superType,
          subRankType,
          subType
        }

        RequestService[func](param).then((result) => {
          let nowList = this.getNowList()
          if (Object.prototype.toString.call(result) !== '[object Array]') {
            if (result.list.length === 0) {
              nowList.isEnd = true
            } else {
              nowList.page = page
            }

            nowList.list = nowList.list.concat(result.list)
            nowList.info = result.info
            this.listNowData = nowList.list
            this.curUserData = Object.prototype.toString.call(result.info) === '[object Array]' ? '' : result.info

            if (page === 1) {
              setTimeout(() => {
                this.scrollToTop()
              }, 100)
            }
          } else {
            nowList.isEnd = true
          }

          this.isRequestIng = false

          setTimeout(() => {
            this.updateListView()
          }, 100)
        }, () => {
          this.isRequestIng = false
          this.listNowData = this.listNowData || []
          this.curUserData = this.curUserData || null
        })
      },

      // 进入载入状态.
      loading () {
        this.locked = true
        this.loadingStatus = true
      },

      getMoreData () {
        let nowList = this.getNowList()
        let page = nowList.page

        this.update(page + 1)
      },

      changeSuperTab (tab) {
        this.changeTab(tab)
      },
      initListData () {
        for (let tab of this.tabs) {
          let type = tab.type
          let subTab = this.subTabs[tab.type]

          this.listData[type] = {}

          if (subTab) {
            for (let tab of subTab.tabs) {
              this.listData[type][tab.type] = {
                page: 1,
                isEnd: false,
                list: [],
                info: null
              }
            }
          } else {
            this.listData[type] = {
              page: 1,
              isEnd: false,
              list: [],
              info: null
            }
          }
        }
      },
      forMobileOnscrollBottom () {
        if (this.forMobileOnscrollBottom.tid) {
          clearTimeout(this.forMobileOnscrollBottom.tid)
        }
        this.forMobileOnscrollBottom.tid = setTimeout(() => {
          if (this.checkListBottomforMobile()) {
            var isEnd = this.checkListEnd()

            if (!isEnd) {
              this.getMoreData()
            }
          }
        }, 20)
      },
      onScrollBottom () {
        var isEnd = this.checkListEnd()

        if (!isEnd) {
          this.getMoreData()
        }
      },
      getNowList () {
        let curType = this.curTab.type
        let curSubType = this.curSubTab ? this.curSubTab.type : ''
        let nowList = this.listData[curType]

        if (nowList[curSubType]) {
          nowList = nowList[curSubType]
        }
        return nowList
      },
      checkListEnd () {
        var nowList = this.getNowList()
        return nowList.isEnd
      },
      checkListBottomforMobile () {
        let scrollTop = this.$els.rankListCtnr.scrollTop
        let listHeight = this.$els.rankListCtnr.clientHeight
        let listAllHeight = this.$els.rankListCtnr.scrollHeight

        if (listAllHeight - scrollTop - listHeight <= 30) {
          console.log(111111111111)
          return true
        }
      },
      updateListView () {
        perfectScrollbar.update(this.$els.rankListCtnr)
      }
    },
    ready: function () {
      Object.assign(this, this.data)

      if (this.type !== 'mobile') {
        perfectScrollbar.initialize(this.$els.rankListCtnr)
      }
      console.log(this.type)
      this.initListData()
      this.setFirstTab()

      if (this.type === 'mobile') {
        console.log(this.$els.rankListCtnr)
        this.$els.rankListCtnr.onscroll = this.forMobileOnscrollBottom
      } else {
        this.$els.rankListCtnr.addEventListener('ps-y-reach-end', this.onScrollBottom)
      }

      this.$watch('changeSuperTab', (newValue, oldValue) => {
        this.changeTab(newValue)
      })

      Vue.filter('changeFace', (src) => {
        if (!src) {
          return ''
        }
        return src.indexOf('noface.gif') > -1 ? src : bfsImgLink(src)
      })
    },
    beforeDestroy () {
      this.$els.rankListCtnr.removeEventListener('ps-y-reach-end', this.onScrollBottom)
    }
  }

</script>



