import Vue from 'vue'
import Vuex from 'vuex'

// 引入模块 Store.
import achieve from './modules/achieve'
import areaList from './modules/area-list'
import currency from './modules/currency'

Vue.use(Vuex)

// Store 导出.
export default new Vuex.Store({
  modules: {
    achieve,
    areaList,
    currency
  }
})



