import { Post } from './srv.http'

export default function (type) {
  if (type) {
    return new Promise((resolve, reject) => {
      Post('/lastOrder/exchange', {
        type: type
      }).then(data => {
        resolve(data)
      }).catch(xhrObject => reject(xhrObject))
    })
  }
}



