<template lang="jade">
  div.content(:class="{'loaded':loaded}")
    div.limn__title
      p 我的绘马板：
        span.num {{ horseScore }}
    div.container__wrap.limn__detail
      div.container__content
        textarea.detail(maxlength="10", v-model="wishContent", placeholder="写下心愿哟~")
    div.limn__btns-box
      div(@click="onClickWish(1)") 挂小绘马
      div(@click="onClickWish(2)") 挂大绘马
      div(@click="onClickWish(3)") 挂特大绘马
    div.limn__ex
      p 活动期间，投喂绘马板道具，即可参与新年绘马活动哦~
      p
        span.bright 1
        span 绘马板，可悬挂
        span.bright 小绘马
      p
        span.bright 500
        span 绘马板，可悬挂
        span.bright 大绘马
      p 12.31日当日赠送绘马板最多的用户，可悬挂特大绘马
      p 绘马板最多输入10个字哦~
    div.limn__pic
      img(:src="limnPic" v-if="loaded")
</template>

<style lang="stylus" scoped>
  .content
    flex: 1
    &.loaded
      .limn__btns-box
        > div:nth-child(1)
          background-image: url(../../images/limn__btn1.png)
          &:hover
            background-image: url(../../images/limn__btn1-hover.png)
        > div:nth-child(2)
          background-image: url(../../images/limn__btn2.png)
          &:hover
            background-image: url(../../images/limn__btn2-hover.png)
        > div:nth-child(3)
          background-image: url(../../images/limn__btn3.png)
          &:hover
            background-image: url(../../images/limn__btn3-hover.png)

  .limn__title
    position: relative
    display: flex
    justify-content: center
    align-items: center
    width: 358px
    height: 56px
    margin: 0 auto
    border: 1px dashed rgba(202,169,126,.4)
    background: rgba(118,61,54,.4)
    border-radius: 30px
    color: #cecdcd
    font-size: 24px
    .num
      color: #edb678
    &::before, &::after
      content: ""
      display: block
      position: absolute
      top: 50%
      width: 16px
      height: 16px
      border: 1px dashed rgba(202,169,126,.4)
      border-radius: 50%
      background: #220e10
      transform: translateY(-50%)
    &::before
      left: 9px
    &::after
      right: 9px
  
  .container__wrap
    width: 100%
    padding: 3px
    box-sizing: border-box
    background: linear-gradient(to bottom, #cdb896, #776f56)
    border-radius: 40px
    overflow: hidden

  .container__content
    height: 100%
    border-radius: 40px
    background-color: #271413
    overflow: hidden
  
  .limn__detail
    position: relative
    z-index: 1
    margin-top: 21px
    height: 111px
    border-radius: 18px
    .container__content
      border-radius: 18px
    .detail
      display: flex
      align-items: center
      width: 100%
      height: 100%
      padding: 38px 33px
      color: #cecdcd
      box-sizing: border-box
      background: transparent
      border: none
      font-size: 16px
  
  .limn__btns-box
    display: flex
    align-items: center
    justify-content: space-around
    width: 100%
    height: 71px
    padding-top: 72px
    margin-top: -72px
    border-radius: 18px
    background: rgba(217,190,154,.1)
    > div
      position: relative
      width: 116px
      height: 36px
      border-radius: 46px
      background-color: #ffe38e
      border: 5px solid #f0b348
      box-shadow: 0 0 0 3px #350828
      text-indent: -9999px
      background-repeat: no-repeat
      background-position: center
      cursor: pointer
      // transition: all .4s ease-in
      &:hover
        background-color: #fffcb1
        border-color: #ffe878
        box-shadow: 0 0 0 3px #643a54

  .limn__ex
    margin-top: 30px
    text-align: center
    color: #cecdcd
    line-height: 1.5
    .bright
      color: #edb678

  .limn__pic
    margin: 27px 0 0 53px
</style>

<script lang="babel">
  import { Wish } from '../../service'
  import Toast from '../toast'
  export default {
    data () {
      return {
        wishContent: '',
        limnPic: require('../../images/limn__pic.png')
      }
    },
    props: [ 'horseScore', 'loaded' ],
    methods: {
      onClickWish (type) {
        const content = this.wishContent.trim()
        if (!content) {
          Toast('绘马板内容不能为空哦！')
          return
        }
        Wish(type, content)
          .then(res => {
            this.wishContent = ''
            Toast('许愿成功')
          })
          .catch(err => {
            Toast(err.json().msg)
          })
      }
    },
    ready () {

    }
  }
</script>


