<template lang="jade">
  div.guest-panel-ctnr
    template(v-for="item in buttons")
      a.top-nav-btn.dp-i-block.v-top.none-select(role="button", :href="item.href", :title="item.title || ''", @click.stop="item.onClick")
        i.v-middle(v-if="item.icon", :class="item.icon")
        span.v-top(v-text="item.label")
      span.nav-btn-divider.dp-i-block(v-if="item.divider")
    div.user-live-ctnr.f-right.f-clear
      enter-room(@click="login")
      user-guide
</template>

<style lang="stylus">
  @import "~@bilibili-live/utils/style/variable.styl"

  $topNavBtnSize = {
    height: 25px
    margin: 12.5px
  }

  .top-nav-btn
    height: $topNavBtnSize.height
    padding: 0 7px
    margin: $topNavBtnSize.margin 2px
    border-radius: 5px
    color: $biliColor.gray
    line-height: $topNavBtnSize.height
    cursor: pointer

    &:hover
      color: $biliColor.cyan

    &:active
      background-color: $biliColor.cyan
      color: #fff

  .nav-btn-divider
    $height = 15px
    background-color: $biliColor.gray
    width: 1px
    height: $height
    margin: ($topNavBtnSize.height - $height * .5) 0

  .user-live-ctnr 
      margin-top: 11px 
      width: 122px 
      height: 32px
      box-sizing: border-box
      box-shadow: 0px 2px 5px rgba(34,23,21,0)
      overflow: hidden
      background-color: #fff 
      border-radius: 2px
      &:hover 
        height: 64px
        transition: all ease-in .3s
        box-shadow: 0px 2px 5px rgba(34,23,21,.2)
        .room-enter-btn
          color: #999
          &:hover 
            color: #23aee6

</style>

<script lang="babel">
  import { liveQuickLogin } from '@bilibili-live/utils'
  import EnterRoom from './Enter-Room.vue'
  import UserGuide from './user-guide.vue'

  export default {
    data () {
      return {
        buttons: [{
          icon: 'live-icon weibo-dark',
          title: '使用微博账号登录',
          href: 'https://passport.bilibili.com/login'
        }, {
          icon: 'live-icon qq-dark',
          title: '使用 QQ 账号登录',
          href: 'https://passport.bilibili.com/login'
        }, {
          label: '登录',
          title: '登录',
          onClick: liveQuickLogin,
          divider: true  // 显示分隔符, 在按钮右侧.
        }, {
          label: '注册',
          title: '注册',
          href: 'http://www.bilibili.com/register'
        }]
      }
    },

    components: { EnterRoom, UserGuide },

    methods: {
      login () {
        liveQuickLogin()
      }
    }
  }

</script>



