<template lang="jade">
  div.section-title
    div.title-container
      span(v-for="item in title[index]" track-by="$index" v-bind:class="['font-' + item]")
</template>

<style lang="stylus" scoped>
  .section-title
    margin: 0 auto
    height: 74px
    line-height: 72px
    text-align: center
    color: #edb576
    font-size: 34px
    text-shadow: 0 3px 1px #5e111b
    letter-spacing: 6px

    .title-container
      position: relative
      display: inline-block
      overflow: hidden

      &:before, &:after
        position: absolute
        display: block
        content: ''
        width: 100%
        height: 2px
        border-radius: 50%
        background-image: linear-gradient(90deg, transparent 0%, rgba(105, 63, 44, 1) 40%, rgba(105, 63, 44, 1) 60%, transparent 100%)
        background-position: 0

      &:before
        top: 0
      
      &:after
        bottom: 0
</style>

<script>
  export default {
    data () {
      return {
        title: {
          1: ['dao', 'ju', 'jie', 'shao'],
          2: ['wo', 'dh', 'yao', 'cheng', 'wei', 'kan', 'ban', 'niang', 'th'],
          3: ['L', 'a', 's', 't', 'o', 'r', 'd', 'e', 'r'],
          4: ['xin', 'nian', 'hui', 'ma'],
          5: ['pai', 'hang', 'bang']
        }
      }
    },
    props: ['index']
  }
</script>


