<script>
  import SectionTitle from '../section-title'
  import LiveStreamer from '../streamer'
  import { Lighten, GetStar } from '../../service'
  import Toast from '../toast'
  export default {
    data () {
      return {
        tabId: 1,
        day: 0,
        hour: 0,
        minute: 0,
        second: 0,
        pic: {
          star: require('../../images/mc__start.png'),
          tree: require('../../images/ms-tree.png'),
          firework: require('../../images/ny__fireworks.png'),
          nyRank: require('../../images/ny-rank.png')
        },
        changTitle: '新年倒计时'
      }
    },
    computed: {
      tabClassObj: function () {
        return {
          'last-order__tab-currentL': this.tabId === 1,
          'last-order__tab-currentR': this.tabId === 2
        }
      }
    },
    props: [ 'starScore', 'countdown', 'isGetStar', 'getIndexData', 'loaded' ],
    components: {
      SectionTitle,
      LiveStreamer
    },
    watch: {
      countdown (val, oldVal) {
        if (oldVal !== '') {
          return
        }
        if (val < 518400) {
          this.tabId = 2
        }
        if (val <= 0) {
          this.changTitle = '新年快乐'
          return
        }
        let tm = val
        const cd = setInterval(() => {
          if (tm <= 0) {
            clearInterval(cd)
            this.changTitle = '新年快乐'
            return
          }
          --tm
          this.day = Math.floor(tm / (24 * 3600))
          const surplusD = tm % (24 * 3600)
          this.hour = Math.floor(surplusD / 3600)
          const surplusH = surplusD % 3600
          this.minute = Math.floor(surplusH / 60)
          this.second = surplusH % 60
        }, 1000)
      }
    },
    methods: {
      onClickTab (id) {
        this.tabId = id
      },
      onClickGetStar () {
        if (this.isGetStar) {
          return
        }
        GetStar()
          .then(res => {
            Toast('领取成功')
            this.getIndexData()
          })
          .catch(error => {
            Toast(error.json().msg)
          })
      },
      onClickLighten () {
        Lighten()
          .then(res => {
            Toast('点亮成功，快去房间页里领扭蛋币吧（￣▽￣）')
            this.getIndexData()
          })
          .catch(error => {
            Toast(error.json().msg)
          })
      }
    },
    ready () {
    }
  }
</script>


