<template lang='jade'>
  a.nav-item.p-relative.pointer(:href="href", :target="target ? target : '_blank'")
    span.label.v-top(v-text='text')
    i.down-arrow.dp-i-block.ts-dot-4(v-if="drawer", :class="{'up': arrowUp === arrowUpNow}")
</template>

<style lang="stylus">
  @import "~@bilibili-live/utils/style/variable.styl";

  $itemSize = {
    height: 50px
  }

  $media = {
    hideLabel: 1050px
  }

  .live-navbar-top
    .nav-item
      display: inline-block
      height: $itemSize.height
      padding: 0 25px
      color: $biliColor.gray
      line-height: $itemSize.height
      font-size: 14px

      &:hover
        color: $biliColor.cyan

    .down-arrow
      width: 12px
      height: 8px
      margin-left: 10px
      background-image: url("../assets/arrow-down.png")

      &.up
        transform: rotate(180deg)

</style>

<script lang='babel'>
    export default {
      data () {
        return {}
      },
      events: {},
      props: ['href', 'target', 'text', 'drawer', 'mouseEnter', 'mouseLeave', 'isMaxthon', 'arrowUp', 'arrowUpNow'],
      ready: function () {
        const element = this.$el
        if (this.mouseEnter) {
          bindPropsEvent.call(this, element, 'mouseEnter', 'mousemove')
        }
        if (this.mouseLeave) {
          bindPropsEvent.call(this, element, 'mouseLeave', 'mouseout')
        }
      }
    }

    function bindPropsEvent (element, eventType, maxthonEventType) {
      const eventHandler = this[eventType].bind(this)
      element.addEventListener(eventType.toLowerCase(), eventHandler)
      this.isMaxthon && element.addEventListener(maxthonEventType, eventHandler)
    }
</script>



