'use strict'

// eslint Setting
/* eslint no-unused-vars: 0 */

// BASIC - js
require('es6-promise').polyfill()
import Vue from 'vue'
import Vuex from 'vuex'
import VueResource from '@bilibili-live/vue-resource'
import PerfectScrollbar from 'perfect-scrollbar'
import LiveNavbarTop from '@bilibili-live/component.navbar-top'
import LiveFooter from '@bilibili-live/component.footer'
import '@bilibili-live/component.icon'
import '@bilibili-live/component.icon-font'

// BASIC - stylesheet
import '../_common/style/style-common.styl'
import '../_common/components/Live-Icon/bundled/entry.styl'
import '../../node_modules/perfect-scrollbar/dist/css/perfect-scrollbar.css'
import '../_common/modules/http-interceptors'
// CUSTOM - style entry
import './page.styl'

// CUSTOM - js
import { Index } from './service'
import { Loading, LiveSidebar, ShiningBackground, HeadScreen, PropsIntro, KanBan, LastOrder, Reward, RankArea, WishBoard, PropsBackground } from './components'
import Toast from './components/toast'

Vue.use(VueResource)

const App = new Vue({
  el: 'body',
  data: {
    scrollVal: 0,
    scrollTarget: null,
    wishScore: '',
    starScore: '',
    horseScore: '',
    countdown: '',
    huima: '',
    kanban: {},
    isGetStar: false,
    loaded: 0
  },
  methods: {
    getIndexData () {
      Index()
      .then(res => {
        this.wishScore = res.wishScore
        this.starScore = res.starScore
        this.horseScore = res.horseScore
        this.countdown = res.countdown
        this.huima = res.huima
        this.kanban = res.kanban
        this.isGetStar = res.isGetStar
      })
      .catch(err => {
        Toast(err.json().msg)
      })
    }
  },
  ready () {
    // Loading first-screen
    Loading().then(res => {
      // Loading other resource after first-screen loaded
      this.loaded = 1
    })
    this.$nextTick(() => {
      PerfectScrollbar.initialize(this.$els.scroller, {
        suppressScrollX: true
      })
      this.scrollTarget = this.$els.scroller
      this.$els.scroller.addEventListener('ps-scroll-y', e => {
        this.scrollVal = e.target.scrollTop
      })
    })
    this.getIndexData()
  },
  components: {
    LiveNavbarTop,
    LiveFooter,
    LiveSidebar,
    ShiningBackground,
    HeadScreen,
    PropsIntro,
    KanBan,
    LastOrder,
    Reward,
    RankArea,
    WishBoard,
    PropsBackground
  }
})




