<template lang="jade">
  div.props-background
    div.snow-container
      div.blur1
        //- left
        i.snow.sl1
        i.snow.sl2
        i.snow.sl3
        //- right
        i.snow.sr1
        i.snow.sr2
        i.snow.sr3
      div.blur2
        //- left
        i.snow.sl4
        i.snow.sl5
        i.snow.sl6
        //- right
        i.snow.sr4        
        i.snow.sr7
        i.snow.sr8

</template>

<style lang="stylus" scoped>
  .props-background
    position: fixed
    width: 100%
    height: 100%
    top: 0
    z-index: 50
    overflow: hidden

  .snow-container
    position: absolute
    width: 1920px
    height: 100%
    top: 0
    left: 50%
    margin-left: -960px

    .blur1
      animation: wave 24s infinite ease
      .snow
        &:after
          background-image: url('../../images/snow_blur.png')

    .blur2
      animation: wave 18s infinite ease   
      .snow
        &:after
          background-image: url('../../images/snow_blur2.png')
 
  .snow
    position: absolute
    display: block
    opacity: 0
    animation-name: flyingsnow
    animation-iteration-count: infinite
    animation-timing-function: ease
    animation-duration: 60s

    &:after
      content: ''
      display: block
      width: 100px
      height: 100px
      background-repeat: no-repeat
      background-size: 100px
      animation: flyingrotate 40s infinite linear
      opacity: .1   
    
    &.sl1, &.sr4
      top: 300px
      animation-duration: 50s
    &.sl1
      animation-delay: 2s    
      left: -20px
    &.sr4
      animation-delay: 8s    
      right: -20px      

    &.sl2, &.sr3
      animation-duration: 70s
      &:after
        width: 80px
        height: 80px
        background-size: 80px
        opacity: .2
    &.sl2
      animation-delay: 1s    
      left: 50px
    &.sr3
      animation-delay: 6s    
      right: 50px

    &.sl3, &.sr2
      animation-duration: 30s           
      &:after
        width: 60px
        height: 60px
        background-size: 60px
        animation-delay: 2s
        opacity: .1
    &.sl3
      animation-delay: 6s     
      left: 200px
    &.sr2    
      right: 200px

    &.sl4, &.sr1
      top: -300px   
      animation-duration: 80s         
      &:after
        width: 120px
        height: 120px
        background-size: 120px
        animation-delay: 6s
        opacity: .12
    &.sl4
      animation-delay: 12s
      left: 160px
    &.sr1   
      right: 160px
    
    &.sl5
      top: -100px
      left: 120px
      animation-duration: 120s
      &:after
        width: 160px
        height: 160px
        background-size: 160px
        opacity: .25

    &.sl6
      top: 80px
      left: 80px
      animation-duration: 55s
      &:after
        width: 110px
        height: 110px
        opacity: .2
    
    &.sr7
      top: -150px
      right: -120px
      animation-duration: 160s
      animation-delay: 8s
      &:after
        width: 320px
        height: 320px
        background-size: 320px
        opacity: .1

    &.sr8
      top: 100px
      right: 20px
      animation-duration: 65s
      animation-delay: 4s
      &:after
        width: 125px
        height: 125px
        background-size: 125px
        opacity: .3

@keyframes wave
  0%
    transform: translateX(0)
  25%
    transform: translateX(60px)
  75%
    transform: translateX(-60px)
  100%
    transform: translateX(0)

@keyframes flyingsnow
  0%
    transform: translateY(0)
    opacity: 0
  10%
    opacity: 1
  90%
    opacity: 1
  100%
    transform: translateY(1000px)  
    opacity: 0

@keyframes flyingrotate
  0%
    transform: rotateZ(0)
  100%
    transform: rotateZ(360deg)

</style>

<script>
  export default {
    props: ['scrollVal'],
    watch: {
      scrollVal (val) {
      }
    }
  }
</script>


