export default {
  // 瓜子 Getter.
  getCurrency (state) {
    return {
      gold: state.currency.gold,
      silver: state.currency.silver
    }
  },

  // 成就值 Getter.
  getAchieve (state) {
    return state.achieve.value
  },

  // 分区列表 Getter.
  getAreaList (state) {
    return state.areaList
  }
}



