<template lang="jade">
  div.reward
    div.tab
      div.reward__cap(:class="{ 'right' : !isUser }")
      div.tab-left(:class="{ tabCurrent: isUser }", @click="onClickUser") 用户
      div.tab-right(:class="{ tabCurrent: !isUser }", @click="onClickAnchor") 主播
    article(v-if="isUser")
      div.container__wrap.reward__sc
        div.container__content
          div.header__title
            span.reward__subtitle-w Santa Claus
          div.sc__word 
            p Santa Claus即圣诞老人，用户榜每日top1用户可以获得Santa Claus头衔。
            p Santa Claus进入直播间，还会受到看板娘的热烈欢迎，谁让我们的22和33都至今还相信这圣诞老人的传说呢。
            p 另外，12月23日-25日之间成为Santa Claus还会额外获得一个“限量版圣诞帽”，赠送给主播可以使主播获得看板娘限量装扮。
          div.sc__reward
            div
              div.sc__reward-img1
              p 奖励Santa Claus头衔
            div
              div.sc__reward-img2
              p 特殊欢迎词
            div
              div.sc__reward-img3
              p 限量版圣诞帽

      div.container__wrap.reward__ranking
        div.container__content
          div.header__title
            span.reward__subtitle-w 排行奖励
          div.ranking__title TOP1
          div.ranking__st1
            div
              div.ranking__st1-img1
              p BiliBili Macro Link VIP票 X2
            div
              div.ranking__st1-img2
              p 大航海总督体验1个月
            div
              div.ranking__st1-img3
              p 小电视泡面碗 X1
            div
              div.ranking__st1-img4
              p 小电视棉拖鞋 X1
          div.ranking__st2
            div.ranking__title TOP2-5
            div.cent
              div.ranking__st2-img1
              p 大航海提督体验1个月
            div.cent
              div.ranking__st2-img2
              p 小电视泡面碗 X1
            div.cent
              div.ranking__st2-img3
              p 小电视棉拖鞋 X1
          div.ranking__st3
            div.ranking__title TOP6-10
            div.cent
              div.ranking__st3-img1
              p 大航海舰长体验1个月
            div.cent
              div.ranking__st3-img2
              p 小电视棉拖鞋 X1
      
      div.container__wrap.reward__bl-title
        div.container__content
          div.header__title
            span.reward__subtitle-w 头衔升级
          div.bl-title__cent
            div
              div.reward__title-img1
              p.reward__title-w 御守头衔
              div 通过普通扭蛋币和梦幻扭蛋币获得
            div
              div.reward__title-img2
              p.reward__title-w 满分御守头衔
              div
                p 升级御守头衔，
                p 头衔经验达到1500万获得
            div
              div.reward__title-img3
              p.reward__title-w 桃花御守头衔
              div
                p 升级御守头衔，
                p 头衔经验达到3500万获得
          div.bl-title__tip
            a(href="//live.bilibili.com/help/#toy", target="_blank") 什么是普通扭蛋与梦幻扭蛋
            a(href="//live.bilibili.com/news/detail/307", target="_blank") 什么是可升级头衔与头衔经验

    article(v-else)
      div.container__wrap.reward__ranking
        div.container__content
          div.header__title
            span.reward__subtitle-w 排行奖励
          div.ranking__title TOP1
          div.ranking__st1
            div
              div.ranking__st1-img1
              p BiliBili Macro Link VIP票 X2
            div
              div.ranking__imgid
              p 直播间3位短位号
            div
              div.ranking__st1-img3
              p 小电视泡面碗 X1
            div
              div.ranking__st1-img4
              p 小电视棉拖鞋 X1
          div.ranking__st2
            div.ranking__title TOP2-5
            div.cent
              div.ranking__imgid
              p 直播间3位短位号
            div.cent
              div.ranking__st2-img2
              p 小电视泡面碗 X1
            div.cent
              div.ranking__st2-img3
              p 小电视棉拖鞋 X1
          div.ranking__st3
            div.ranking__title TOP6-10
            div.cent
              div.ranking__imgid.ranking__imgid2
              p 直播间4位短位号
            div.cent
              div.ranking__st3-img2
              p 小电视棉拖鞋 X1
          p.number-tip 短位号说明：①放映厅参与活动排名，但不能获得短位号奖励  ②若主播已经获得短位号，3位短位号不顺延，4位短位号顺延
</template>

<style lang="stylus" scoped>
@import "../../style/variable.styl"
  .reward
    position: relative
    width: 1160px
    margin: 0 auto
    z-index: 1

    &.loaded
      .reward__cap
        &:after
          background: url(../../images/reward__cap.png) no-repeat
      .reward__subtitle-w
        background-image: url(../../images/reward__title-w.png)
      .sc__reward-img1
      .sc__reward-img2
      .sc__reward-img3
        background-image: url(../../images/reward__sc.png)
        background-repeat: no-repeat
      .ranking__title
        background-image: url(../../images/reward__sort.png)
      .ranking__st1
        &::after
        &::before
          background-image: url(../../images/bg-light.png)
      .ranking__st1-img1
      .ranking__st1-img2
      .ranking__st1-img3
      .ranking__st1-img4
      .ranking__st2-img1
      .ranking__st2-img2
      .ranking__st2-img3
      .ranking__st3-img1
      .ranking__st3-img2
        background-image: url(../../images/reward__sort.png)
        background-repeat: no-repeat
      .ranking__st2
      .ranking__st3
        &::after
          background-image: url(../../images/bg-light.png)
        .ranking__title
          background-image: url(../../images/reward__sort.png)
      .ranking__imgid
        background-image: url(../../images/ranking__imgid.png)
      .ranking__imgid2
        background-image: url(../../images/ranking__imgid2.png)        
      .sc__reward
      .bl-title__cent
        > div
          &::before
            background-image: url(../../images/bg-light.png)
      .reward__title-img1
      .reward__title-img2
      .reward__title-img3
        background-image: url(../../images/reward__title.png)

  .tab
    position: relative
    width: 540px
    margin: 0 auto
    padding-top: 100px

  .reward__cap
    position: absolute
    left: 253px
    top: 67px
    z-index: 10
    display: block
    width: 86px
    height: 63px
    transition: .4s linear

    &:after
    &:before
      display: block
      content: ''
      position: absolute
      animation: .4s highjump1 cubic-bezier(0.76, 0.47, 0.08, 0.39)

    &:after
      width: 86px
      height: 63px
      top: 0
      left: 0
    
    &:before
      width: 30px
      height: 30px
      top: 9px
      left: 11px
      box-shadow: -2px 2px 48px #000
      border-radius: 50%
    
    &.right
      transform: translate3d(224px, 0, 0)
      &:after
      &:before
        animation: .4s highjump cubic-bezier(0.76, 0.47, 0.08, 0.39)

  .tab-left, .tab-right
    position: relative
    display: inline-block
    width: 270px
    height: 60px
    color: #662228
    background: #9c4549
    border-radius: 50px
    text-align: center
    line-height: 60px
    font-size: 24px
    vertical-align: middle
    cursor: pointer
  
  .tab-left
    right: -23px
  
  .tab-right
    left: -23px

  .tabCurrent
    background: #cfb798
    color: #663139
    z-index: 9
    transition: all .3s ease-in
    transform: translate3D(0, -7px, 0)
    box-shadow: 0 0 32px rgba(0, 0, 0, .5), 0 0 2px #fff inset

  .container__wrap
    width: 100%
    padding: 3px
    box-sizing: border-box
    background: linear-gradient(to bottom, #cdb896, #776f56)
    border-radius: 40px
    overflow: hidden

  .container__content
    height: 100%
    border-radius: 40px
    background-color: #271413
    overflow: hidden
    createSectionStripe()

  .reward__sc
    margin-top: 45px
    height: 390px
  
  .header__title
    height: 57px
    line-height: 57px
    background-color: rgba(0,0,0,.25)
    color: #ffc589
    text-align: center
    font-size: 20px
  .reward__subtitle-w
    display: inline-block
    width: 178px
    height: 26px
    margin-top: 14px
    text-indent: -9999px
    background-repeat: no-repeat

  .sc__word
    padding-top: 20px
    font-size: 12px
    line-height: 1.5
    text-align: center
    color: #aba8a9

  .sc__reward
    display: flex
    margin-top: 57px
    color: #a79b9a
    > div
      position: relative
      flex: 1
      text-align: center
      &::before
        content: ""
        position: absolute
        top: -104px
        left: 48px
        width: 360px
        height: 309px
        background-repeat: no-repeat
        background-size: 100% 100%
        z-index: 0
    > div:nth-child(2)::before
      left: 0
    > div:nth-child(3)::before
      left: 0
    .sc__reward-img1, .sc__reward-img2, .sc__reward-img3
      position: relative
      display: inline-block
      width: 100%
      height: 114px
      margin-bottom: 23px
      z-index: 1
    .sc__reward-img1
      background-position: 111px 0
      & + p
        padding-left: 57px
    .sc__reward-img2
      background-position: -285px 0
      & + p
        padding-right: 21px
    .sc__reward-img3
      background-position: -647px 0
      & + p
        padding-right: 51px

  .reward__ranking
    margin-top: 45px
    .reward__subtitle-w
      width: 133px
      height: 29px
      background-position: 0 -27px
  .ranking__title
    margin-top: 45px
    margin-bottom: 23px
    color: #d7a474
    text-align: center
    text-indent: -9999px
    background-repeat: no-repeat
    background-position: center top

  .ranking__st1
    position: relative
    display: flex
    color: #a79b9a
    justify-content: center
    margin-bottom: 40px
    text-align: center
    &::before
    &::after
      content: ""
      position: absolute
      background-repeat: no-repeat
      background-size: 100% 100%
      z-index: 0
    &::before
      top: -102px
      left: 27px
      width: 600px
      height: 300px
    &::after
      top: -39px
      right: -63px
      width: 900px
      height: 200px

    > div
      position: relative
      z-index: 1
  .ranking__st1-img1, .ranking__st1-img2, .ranking__st1-img3, .ranking__st1-img4
    height: 122px
    display: inline-block
  .ranking__st1-img1
    width: 357px
    background-position: -20px -36px
  .ranking__st1-img2
    width: 183px
    background-position: -380px -36px
  .ranking__st1-img3
    width: 163px
    background-position: -556px -36px
  .ranking__st1-img4
    width: 158px
    background-position: -721px -36px
  .ranking__st2, .ranking__st3
    display: inline-block
    color: #a79b9a
    text-align: center
    margin-bottom: 85px
  .ranking__st2
    width: 60%
    position: relative
    &::after
      content: ""
      position: absolute
      top: -30px
      left: -133px
      width: 900px
      height: 300px
      opacity: 0.8
      background-repeat: no-repeat
      background-size: 100% 100%
      z-index: 0
    > div
      position: relative
      z-index: 1
    .ranking__title
      background-repeat: no-repeat
      background-position: 139px -256px
    .cent
      display: inline-block
  .ranking__st2-img1, .ranking__st2-img2, .ranking__st2-img3
    height: 122px
    display: inline-block
  .ranking__st2-img1
    width: 169px
    background-position: 25px -284px
  .ranking__st2-img2
    width: 159px
    background-position: -129px -284px
  .ranking__st2-img3
    width: 165px
    background-position: -291px -284px
  .ranking__st3
    width: 40%
    text-align: left
    position: relative
    &::after
      content: ""
      position: absolute
      top: -30px
      left: -308px
      width: 900px
      height: 300px
      opacity: 0.8
      background-repeat: no-repeat
      background-size: 100% 100%
      z-index: 0
    > div
      position: relative
      z-index: 1
    .ranking__title
      background-repeat: no-repeat
      background-position: -560px -256px
    .cent
      display: inline-block
      p
        text-align: center
  .ranking__st3-img1, .ranking__st3-img2
    height: 122px
    display: inline-block
  .ranking__st3-img1
    width: 156px
    background-position: -571px -284px
  .ranking__st3-img2
    width: 208px
    background-position: -726px -284px
  .ranking__imgid
    width: 155px
    height: 122px
    background-repeat: no-repeat
    background-position: center
  .reward__bl-title
    margin-top: 45px
    .reward__subtitle-w
      width: 133px
      height: 29px
      background-position: 0 -56px
  .bl-title__cent
    margin-top: 42px
    display: flex
    color: #cecfcc
    text-align: center
    padding: 0 40px
    > div
      position: relative
      flex: 1
      text-align: center
      &::before
        content: ""
        position: absolute
        top: -112px
        left: -81px
        width: 560px
        height: 300px
        opacity: 0.8
        background-repeat: no-repeat
        background-size: 100% 100%
        z-index: 0

  .reward__title-img1, .reward__title-img2, .reward__title-img3
    position: relative
    z-index: 1
    display: inline-block
    width: 214px
    height: 100px
    margin-bottom: 20px
    background-repeat: no-repeat
  .reward__title-img1
    background-position: 0
  .reward__title-img2
    background-position: -307px -10px
  .reward__title-img3
    background-position: -621px -10px
  .reward__title-w
    font-size: 18px
    font-weight: bold
    padding-bottom: 15px


  .bl-title__tip
    position: relative
    display: flex
    width: 50%
    margin: 58px auto
    color: #c89863
    > a
      display: block
      flex: 1
      color: #c89863
      text-align: center
      text-decoration: underline
  .reward__ranking .number-tip
    color: #a79b9a
    text-align: center
    margin-bottom: 48px
</style>

<script lang="babel">
  import Toast from '../toast'
  export default {
    data () {
      return {
        isUser: true,
        toastContent: 'Toast'
      }
    },
    components: {
      Toast
    },
    methods: {
      onClickUser () {
        this.isUser = true
      },
      onClickAnchor () {
        this.isUser = false
      }
    }
  }
</script>


