
<template lang="jade">
  p.text-node.t-over-hidden.t-nowrap.f-clear
    span.v-middle.f-right.value-color(v-text="value")
    i.live-icon.v-middle(:class="iconName")
    span.v-middle(v-text="label")
  slot
</template>

<style lang="stylus" scoped>
  @import "~@bilibili-live/utils/style/variable.styl";

  .value-color
    color: $biliColor.darkgray

  .text-node
    margin: 5px 0

    .live-icon
      margin-right: 5px
</style>

<script>
    export default {
      props: ['label', 'iconName', 'value']
    }
</script>



