import { Get } from './srv.http'

export default function (type) {
  return new Promise((resolve, reject) => {
    Get('/lastOrder/getWishingList?type=' + type).then(data => {
      resolve(data)
    }).catch(xhrObject => reject(xhrObject))
  })
}



