import * as mutationsType from './mutation-types'

export default {
  /*
   *  瓜子数量
   *  @ { gold: Number, silver: Numer }
   */
  setCurrency ({ dispatch, state }, { gold, silver }) {
    dispatch(mutationsType.SETCURRENCY, { gold, silver })
  },

  /*
   *  成就值 Setter.
   *  @ Number.
   */
  setAchieve ({ dispatch, state }, achieve) {
    dispatch(mutationsType.SETACHIEVE, achieve)
  }
}




