import { SETCURRENCY } from '../mutation-types'

const defaultValue = '--'

const state = { gold: defaultValue, silver: defaultValue }

const mutations = {
  [SETCURRENCY] (state, { gold, silver }) {
    if (gold !== undefined) { state.gold = gold }
    if (silver !== undefined) { state.silver = silver }
  }
}

export default { state, mutations }



