<template lang="jade">
  div.shining-background
    //- props
    div.props(v-bind:class='{"active": index == 1}')
      i.bls.left
      i.blb.right
    //- kan ban
    div.kan-ban(v-bind:class='{"active": index == 2}')
      i.bls.left
      i.bls.right
    //- last order
    div.last-order(v-bind:class='{"active": index == 3}')
      i.blb.center
    //- wish board
    div.wish-board(v-bind:class='{"active": index == 4}')
      i.blb.top
      i.blb.bottom
    //- reward
    div.reward(v-bind:class='{"active": index == 5}')
      i.blb.top
      i.blb.middle
      i.blb.bottom

</template>

<style lang="stylus" scoped>
  .shining-background
    position: absolute
    top: -700px
    left: 50%
    margin-left: -960px
    width: 1920px
    height: 8000px
    z-index: 0

    i
      display: block
      position: absolute
      opacity: 0
      transition: 1.2s .2s ease

    .bls
      width: 888px
      height: 888px
      background-image: url('../../images/bl_small.png')
      background-repeat: no-repeat

    .blb
      width: 866px
      height: 866px
      background-image: url('../../images/bl_big.png')
      background-repeat: no-repeat

    .props
      .left
        top: 1700px
        left: 120px

      .right
        top: 1800px
        left: 840px

      &.active
        .left, .right
          opacity: .8
    
    .kan-ban
      .left
        top: 3000px
        left: 400px
      
      .right
        top: 3000px
        left: 800px

      &.active
        .left, .right
          opacity: .9
        .right
          transform: scale(1.2, .6)
    
    .last-order
      .center
        top: 3700px
        left: 300px

      &.active
        .center
          opacity: 1
          transform: scale(1.8, 1.5)
    
    .wish-board
      .top
        top: 4600px
        left: 360px
      .bottom
        top: 5400px
        left: 800px      
      
      &.active
        .top, .bottom
          opacity: .8
          transform: scale(1.6)
    
    .reward
      .top
        top: 5700px
        left: 200px
      .middle
        top: 6100px
        left: 0
      .bottom
        top: 6850px
        left: 1000px

      &.active
        .top, .middle, .bottom
          opacity: .9

</style>

<script>
  export default {
    data () {
      return {
        index: 0
      }
    },
    props: ['scrollVal'],
    watch: {
      scrollVal (val) {
        // console.log(val, this.index)
        if (val > 600 && val <= 1700) {
          this.index = 1
        } else if (val > 1700 && val <= 2600) {
          this.index = 2
        } else if (val > 2600 && val <= 3600) {
          this.index = 3
        } else if (val > 3600 && val <= 4600) {
          this.index = 4
        } else if (val > 4600 && val <= 6300) {
          this.index = 5
        } else {
          this.index = 0
        }
      }
    }
  }
</script>



