<!--
    Download Component By LancerComet at 15:45, 2016/7/20.
    # Carry Your World #
-->

<template lang="jade">
  div.download-panel.p-absolute(v-show="show", transition="fade")
    div(v-el:link)
      a.dp-none.a-move-in-top(target="_blank", v-for="item in items", @mouseenter="mouseenter(item)", @mouseover="isMaxthon ? mouseenter(item) : 0", @mouseleave="mouseleave", @mouseout="isMaxthon ? mouseleave : 0", :href="item.link")
        div.dp-i-block.item-ctnr.p-relative.border-box.t-center
          div.title(v-text="item.label + '下载'")
          div.qr-code(role="img", :style="{'background-image': 'url(' + item.image + ')'}")
          div.divider-v.p-absolute(v-if="$index % 2 === 0")

</template>

<style lang="stylus">
  @import "~@bilibili-live/utils/style/variable.styl";

  $qrItemSize = {
    width: 228px
    height: 228px
  }

  $panelSize = {
    width: 456px
  }

  $qrCodeSize = 157px

  .download-panel
    width: $panelSize.width
    padding: 10px 0
    left: 233px
    border: 1px solid #ddd
    border-top: 0
    border-radius: 0 0 5px 5px
    background-color: #fff

  .item-ctnr
    width: $qrItemSize.width
    height: $qrItemSize.height
    padding: 10px
    font-size: 14px
    color: $biliColor.gray

    &:hover
      color: $biliColor.cyan

    .qr-code
      width: $qrCodeSize
      height: $qrCodeSize
      margin: 15px auto

    .divider-v
      width: 1px
      height: 50%
      top: 0
      right: 0
      margin: 25% 0
      background-color: lighten($biliColor.gray, 60%)

</style>

<script>
  import EventBus from 'src/event-bus'
  import { SeqAnimation } from '@bilibili-live/utils'

  import config from '../../config/config.download.js'
  import NavItem from '../Nav-Item.vue'
  import eventName from '../../config/config.event-name'

  // 进入动画控制对象.
  var seqAni = null

  export default {
    data () {
      return {
        items: config
      }
    },

    methods: {
      mouseenter () {
        EventBus.$emit(eventName.showDownloadPanel)
      },
      mouseleave () {
        EventBus.$emit(eventName.hideDownloadPanel)
      }
    },

    components: { NavItem },

    props: ['isMaxthon', 'show'],

    watch: {
      show (value) {
        if (!value) { return }
        if (!seqAni) { seqAni = new SeqAnimation(Array.prototype.slice.call(this.$els.link.children), 'inline-block') }
        seqAni.reset()
        seqAni.seqIn()
      }
    }
  }
</script>



