<template lang="jade">
  div.sidebar
    div.top
    a.item.br(@click="jump(1)")
      p 道具
      p 介绍
    a.item(@click="jump(2)") 看板娘
    a.item.br(@click="jump(3)")
      p last
      p order
    a.item.br(@click="jump(4)")
      p 新年
      p 绘马
    a.item(@click="jump(5)") 排行榜
    a.item(@click="jump(6)") 评论
    a.item(@click="jump(0)") 回顶部
    div.line
</template>

<style lang="stylus" scoped>
  @import "../../style/variable.styl"
  .sidebar
    position: fixed
    right: 10px
    top: 50%
    margin-top: -275px
    width: 72px
    height: 550px
    z-index: 199

    .line
      position: absolute
      left: 30px
      top: 54px
      width: 1px
      height: 420px
      background: #ccb194
      z-index: -1

    .top
      margin-left: 3px
      width: 53px
      height: 60px
      background: url('../../images/sidebar_star.png')

    .item
      position: relative
      display: block
      width: 62px
      height: 62px
      line-height: 60px
      margin-bottom: 5px
      box-sizing: border-box
      text-align: center
      border: 1px solid #ffe9b7
      color: rgba(255, 233, 183, .8)
      border-radius: 50%
      background-color: #4a1823
      font-size: 14px
      user-select: none
      transition: .3s ease
      cursor: pointer
      transform: scale(.95)
      createSidebarStripe()

      &:hover
        color: rgba(254, 255, 223, 1)
        transform: scale(1)
        border-color: #feffdf
        box-shadow: 0 0 12px #feffdf

      &.br
        padding-top: 11px

      > p
        line-height: 1.25em
</style>

<script>
  let pos = [0, 1200, 2200, 3100, 4150, 7100, 8000]
  export default {
    methods: {
      jump (val) {
        let fix = 0
        if (document.querySelector('.reward .tab-left').className !== 'tab-left tabCurrent' && val > 4) {
          fix = -900
        }
        this.target.scrollTop = pos[val] + fix
      }
    },
    props: ['target']
  }
</script>


