<template lang="jade">
  div.wish
    section-title(:index='4')
    p.wish__t-tip 在绘马上写下之后一年的心愿可是新年的大事
    live-streamer(type="big")
      div.steamer__title 谁将获得特大绘马？
      p.steamer__word
        span {{ huima.uname }}
        span {{ huima.score }}绘马板
    div.container
      Plate(:class="{'loaded':loaded}")
      Limn(:horse-score="horseScore", :loaded="loaded")
</template>

<style lang="stylus" scoped>
  .wish
    position: relative
    width: 1160px
    margin: 0 auto
    z-index: 1

  .wish__t-tip
    font-size: 14px
    color: #cecdcd
    text-align: center
    margin: 20px 0 45px 0
  .steamer__title
    padding: 16px 0 20px 0
    text-align: center
    color: #cecdcd
    font-size: 18px
    line-height: 1
  .steamer__word
    color: #e38f59
    font-size: 16px
    text-align: center
    line-height: 1
    > span:last-child
      margin-left: 55px
  .container
    margin-top: 80px
    display: flex
</style>

<script lang="babel">
  import SectionTitle from '../section-title'
  import LiveStreamer from '../streamer'
  import Plate from './plate'
  import Limn from './limn'
  export default {
    data () {
      return {
      }
    },
    props: [ 'horseScore', 'huima', 'loaded' ],
    components: {
      LiveStreamer,
      Plate,
      Limn,
      SectionTitle
    },
    methods: {
    }
  }
</script>


