<!--
    Area Nav Panel By LancerComet at 15:08, 2016.07.08
    # Carry Your World #
    ---
    分区导航面板.
-->

<template lang="jade">
  div.area-nav-panel.p-absolute.over-hidden.none-select(v-show="show", @mouseenter.stop="mouseEnter", @mouseover.stop="isMaxthon ? mouseEnter : 0", @mouseleave.stop="mouseLeave", @mouseout.stop="isMaxthon ? mouseLeave : 0", transition="fade")

    div.p-relative

      //- 分区导航列表.
      ul.area-list.list-none(v-el:area-list)
        a.area-list-item.t-over-hidden.t-nowrap.v-top.border-box.a-move-in-left.ts-dot-2(v-for="item in areaList", :href="item.href", v-if="!item.skipNav")
          li
            i.v-middle(:class="item.iconName")
            span.v-middle(v-text="item.text")

        hr.w-100

      //- 静态导航列表.
      div(v-el:static-list)
        a.static-link.t-center.v-top.a-move-in-left.border-box.ts-dot-2(v-for="item in staticLinks", v-text="item.text", :href="item.href")


</template>

<style lang="stylus">
  @import "~@bilibili-live/utils/style/variable.styl";

  $areaListItem = {
    width: 126px
    height: 40px
    margin: 5px
  }

  $areaNavPanel = {
    width: $areaListItem.width * 2 + $areaListItem.margin
  }

  .area-nav-panel
    width: $areaNavPanel.width
    padding: 10px
    background-color: #fff
    border: 1px solid #ddd
    border-top: 0
    border-radius: 0 0 10px 10px
    animation-duration: .2s !important

    // Vue Transition.
    &.fade-enter
      animation: fade-in linear .2s

    &.fade-leave
      animation: fade-out linear .2s forwards

    hr
      border-top: 0
      border-color: lighten($biliColor.gray, 60%)
      margin: 5px 0

    .area-list-item
    .static-link
      width: $areaListItem.width
      height: $areaListItem.height
      border-radius: 5px
      line-height: $areaListItem.height
      text-decoration: none
      color: $biliColor.gray

      &:hover
        //color: $biliColor.cyan !important
        background-color: $biliColor.hovergray

      &:active
        background-color: $biliColor.blue
        color: #fff !important

      .live-icon-16
        margin-right: 10px

    .area-list-item
      width: $areaListItem.width
      padding-left: 20px

      &:nth-child(odd)
        margin-right: $areaListItem.margin

    .static-link
      width: 100%

    // .md-circle-merge
    //   width: 100%
    //   padding-top: 100%
    //   border-radius: 50%
    //   position: absolute
    //   top: 0
    //   left: 0
    //   margin-top: -50%
    //   background-color: #fff
    //   animation: md-circle-merge $cubicEase 1s
    //   visibility: hidden

    // @keyframes md-circle-merge
    //   0%
    //     visibility: visible
    //     transform: scale(0)

    //   100%
    //     visibility: visible
    //     transform: scale(4)

</style>

<script>
  import Vue from 'vue'
  import Vuex from 'vuex'

  import { SeqAnimation } from '@bilibili-live/utils'
  import staticLinks from '../config/config.static-link.js'

  import getters from 'Vuex/getters'
  import store from 'Vuex/store'

  import EventBus from 'src/event-bus'
  import eventName from '../config/config.event-name'

  Vue.use(Vuex)

  var seqAni = { link: null, staticLink: null }

  export default {
    data () {
      return {
        staticLinks: staticLinks,  // 静态导航列表数据.
        hideTimeout: null
      }
    },

    methods: {
      mouseEnter () {
        EventBus.$emit(eventName.showAreaNavPanel)
      },

      mouseLeave () {
        EventBus.$emit(eventName.hideAreaNavPanel)
      }
    },

    props: ['show', 'isMaxthon'],

    watch: {
      show (value) {
        if (!value) { return }

        // Action: 初始化设置 Seq Animation.
        if (!seqAni.link) { seqAni.link = new SeqAnimation(Array.prototype.slice.call(this.$els.areaList.children), 'inline-block', 40) }
        if (!seqAni.staticLink) { seqAni.staticLink = new SeqAnimation(Array.prototype.slice.call(this.$els.staticList.children), 'block', 40) }

        seqAni.staticLink.reset()
        seqAni.link.seqIn().then(() => seqAni.staticLink.seqIn())
      }
    },

    store,

    vuex: {
      getters: {
        areaList: getters.getAreaList
      }
    }
  }
</script>



