<template lang="jade">
  div.kan-ban(:class="{'loaded':loaded}")
    section-title(:index='2')
    live-streamer
      p
        | 已经对枯燥的连击功能厌倦了吗？那就快来尝试一下
        span.hl 看板娘
        | 功能，让萌萌哒看板娘来帮大家答谢礼物吧！
      p 
        | 通过收集更多的活动道具，还能升级看板娘的装扮，让我们趁着节日，给22和33玩下
        span.hl 换装play
        | ~
    div.kb__container
      div.kb__model
        div.kb__model-img(v-if="loaded")
          img(:src="pic.cloth22", alt="", @click="onClickRole(22)")
          img(:src="pic.cloth33", alt="", @click="onClickRole(33)")
        div.kb__model-p(v-if="loaded")
          span.gif-loading(v-if="isGifCompelete") 动图加载中...
          img.img(:src="clothes.img", alt="")
      div.kb__clothes
        div.kb__title
          p 剩余祝福值：
            span.num {{ wishScore }}
        div.kb__clothes-box
          div
            p.clothing-tit 头饰
            div.kb__clothing
              div.clothing-img
                div.headwear-img
              p
                span.span 2K
                | 祝福值
              div.btn(:class="{dis: kanban[dataObj[clothes.role][0]]}", @click="onClickHeadWear") 兑换
          div
            p.clothing-tit 上装
            div.kb__clothing
              div.clothing-img
                div(:class="clothes.jacket")
              p
                span.span 15W
                | 祝福值
              div.btn(:class="{dis: kanban[dataObj[clothes.role][1]]}",@click="onClickJacket") 兑换
          div
            p.clothing-tit 下装
            div.kb__clothing
              div.clothing-img
                div(:class="clothes.trousers")
              p
                span.span 15W
                | 祝福值
              div.btn(:class="{dis: kanban[dataObj[clothes.role][2]]}",@click="onClickTrousers") 兑换
    div.kb__tip
      p
        | 注：关于看板娘的设置，可以在直播中心我的直播间 - 
        a(href="//live.bilibili.com/i/kanbanmusume", target="_blank") 我的看板娘 
        | 内切换，变换装束。看板娘将替代原有的连击效果。
</template>

<style lang="stylus" scoped>
  .kan-ban
    position: relative
    height: 900px
    width: 1160px
    margin: 0 auto
    z-index: 1

    .live-streamer
      margin-top: 35px
      padding-top: 8px
    
    &.loaded
      .headwear-img
        background-image: url(../../images/2233_headwear.png)
      .jacket-img-22
        background-image: url(../../images/22_jacket.png)
      .jacket-img-33
        background-image: url(../../images/33_jacket.png)
      .trousers-img-22
        background-image: url(../../images/22_trousers.png)
      .trousers-img-33
        background-image: url(../../images/33_trousers.png)
      .kb__clothing
        .btn
          background-image: url(../../images/conversion__btn.png)
          &:hover
            background-image: url(../../images/conversion__btn-hover.png)
        .dis
          background-image: url(../../images/conversion__btn-dis.png)
          &:hover
            background-image: url(../../images/conversion__btn-dis.png)
  
  .kb__container
    display: flex
    width: 1160px
    margin: 70px auto 0 auto
  .kb__model
    width: 560px
  .kb__model-img
    display: inline-block
    width: 147px
    margin-left: 68px
    margin-right: 36px
    img
      padding: 10px 0
      cursor: pointer
  .kb__model-p
    position: relative
    display: inline-block
    width: 300px
    height: 401px
    .gif-loading
      position: absolute
      display: block
      width: 100%
      height: 100%
      z-index: 2
      color: #cfcdcd
    .img
      position: absolute
      height: 400px
      top: 12px
      left: -23px
      z-index: 1
    &::before
      content: ""
      position: absolute
      width: 123px
      height: 35px
      background: rgba(0,0,0,0.2)
      border-radius: 200%
      border: 1px solid rgba(0,0,0,.2)
      left: 65px
      bottom: -20px
      z-index: 0
  .kb__clothes
    flex: 1
  .kb__title
    position: relative
    display: flex
    justify-content: center
    align-items: center
    width: 358px
    height: 56px
    margin: 0 auto
    border: 1px dashed rgba(202,169,126,.4)
    background: rgba(118,61,54,.4)
    border-radius: 30px
    color: #cecdcd
    font-size: 24px
    .num
      color: #edb678
    &::before, &::after
      content: ""
      display: block
      position: absolute
      top: 50%
      width: 16px
      height: 16px
      border: 1px dashed rgba(202,169,126,.4)
      border-radius: 50%
      background: #220e10
      transform: translateY(-50%)
    &::before
      left: 9px
    &::after
      right: 9px
  
  .kb__clothes-box
    display: flex
    padding-top: 65px
    > div
      flex: 1
      text-align: center
    .clothing-tit
      margin-bottom: 22px
      color: #cea574
      font-size: 24px
  .kb__clothing
    position: relative
    display: inline-block
    box-sizing: border-box
    width: 116px
    height: 116px
    margin-top: 90px
    padding-top: 58px
    background: rgba(125,28,46,.5)
    .clothing-img
      position: absolute
      width: 142px
      height: 142px
      top: -97px
      left: -13px
      > div
        position: relative
        display: inline-block
        width: 142px
        height: 142px
        background-repeat: no-repeat
    p
      font-size: 14px
      color: #ebe2be
    .span
      font-size: 16px
      margin-right: 5px
    .btn
      cursor: pointer
      width: 106px
      height: 36px
      margin-top: 13px
      border-radius: 46px
      background-color: #ffe38e
      border: 5px solid #f0b348
      box-shadow: 0 0 0 3px #350828
      text-indent: -9999px
      background-repeat: no-repeat
      background-position: center
      &.dis
        background-color: #d2d2d2
        border-color: #b2b2b2
        box-shadow: 0 0 0 3px #373737
        &:hover
          background-color: #d2d2d2
          border-color: #b2b2b2
          box-shadow: 0 0 0 3px #373737
          // transition: all .4s ease-in        
      &:hover
        background-color: #fffcb1
        border-color: #ffe878
        box-shadow: 0 0 0 3px #643a54
        // transition: all .4s ease-in   
  .kb__tip
    margin-top: 80px
    text-align: center
    p
      display: inline-block
      padding: 15px 20px
      line-height: 1
      color: #c27d72
      font-size: 12px
      border-radius: 48px
      background-color: rgba(255,165,165,0.1)
      &::first-letter
        font-size: 18px
    a
      color: #e9b775
      text-decoration: underline
</style>

<script>
  import SectionTitle from '../section-title'
  import LiveStreamer from '../streamer'
  import { Exchange } from '../../service'
  import Toast from '../toast'
  export default {
    data () {
      return {
        clothes: {
          role: 22,
          jacket: 'jacket-img-22',
          trousers: 'trousers-img-22',
          img: require('../../images/221.gif')
        },
        isGifCompelete: false,
        dataObj: {
          22: [1, 3, 5],
          33: [2, 4, 6]
        },
        pic: {
          cloth22: require('../../images/22clothing.png'),
          cloth33: require('../../images/33clothing.png')
        }
      }
    },
    props: [ 'wishScore', 'getIndexData', 'kanban', 'loaded' ],
    components: {
      SectionTitle,
      LiveStreamer
    },
    methods: {
      onClickRole (param) {
        this.clothes = {
          role: param,
          jacket: 'jacket-img-' + param,
          trousers: 'trousers-img-' + param,
          img: require('../../images/' + param + '1.png')
        }
        this.isGifCompelete = true
        let imgGif = new Image()
        imgGif.src = require('../../images/' + param + '1.gif')
        if (imgGif.compelete) {
          this.loadingGif(param, imgGif)
          return
        }
        imgGif.onload = () => {
          this.loadingGif(param, imgGif)
        }
      },
      onClickHeadWear () {
        this.subExChange(this.dataObj[this.clothes.role][0])
      },
      onClickJacket () {
        this.subExChange(this.dataObj[this.clothes.role][1])
      },
      onClickTrousers () {
        this.subExChange(this.dataObj[this.clothes.role][2])
      },
      subExChange (type) {
        if (this.kanban[type]) {
          return
        }
        Exchange(type)
          .then(res => {
            Toast('兑换成功，快去直播中心装扮吧(｡･ω･｡)')
            this.getIndexData()
          })
          .catch(error => {
            Toast(error.json().msg)
          })
      },
      loadingGif (param, imgGif) {
        this.clothes = {
          role: param,
          jacket: 'jacket-img-' + param,
          trousers: 'trousers-img-' + param,
          img: imgGif.src
        }
        this.isGifCompelete = false
      }
    }
  }
</script>


